import React from "react";
import FormInput from "./FormInput";
import FormContainer from "./FormContainer";
import { bank, toObj } from "./config";

class BankForm extends React.Component {
  state = { ...toObj(bank), ...this.props.info };

  handleChange = ev => {
    this.setState({ [ev.target.name]: ev.target.value });
    this.props.update({ [ev.target.name]: ev.target.value });
  };

  render() {
    const { required } = this.props;
    return (
      <FormContainer>
        <FormInput
          value={this.state[bank[0]]}
          type="text"
          name={bank[0]}
          label="BANCO"
          onChange={this.handleChange}
          w="3"
          required={required}
        />
        <FormInput
          value={this.state[bank[1]]}
          type="text"
          name={bank[1]}
          label="CÓDIGO"
          onChange={this.handleChange}
          w="1"
          required={required}
        />
        <FormInput
          value={this.state[bank[2]]}
          type="text"
          name={bank[2]}
          label="AGÊNCIA"
          onChange={this.handleChange}
          w="1"
          required={required}
        />
        <FormInput
          value={this.state[bank[3]]}
          type="text"
          name={bank[3]}
          label="CONTA"
          onChange={this.handleChange}
          w="3"
          required={required}
        />
      </FormContainer>
    );
  }
}

export default BankForm;
