import React from "react";
import styled from "styled-components";
import minusIcon from "assets/icons/minus-circle-fill.png";
import DeleteButton from "components/common/DeleteButton";

const Div = styled.div`
  display: flex;
  cursor: default;
  align-items: center;
  padding: 15px;
  padding-bottom: 0;
  font-size: 20px;
  line-height: 22px;
  > img {
    margin-right: 5px;
  }
  .info {
    flex-grow: 1;
    min-width: 0;
  }
  h4 {
    font: inherit;
    font-weight: 700;
  }
  h5 {
    font: inherit;
    font-weight: 300;
  }
  .icon-seta {
    transform: rotate(${({ priceOpen }) => priceOpen && 180}deg);
  }
  @media (max-width: 768px) {
    font-size: 15px;
    padding: 10px;
    h4,
    h5 {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
`;

const ProductInfo = ({ editMode, type, material, width, depth, ...props }) => {
  const [deleteConfirm, setDelete] = React.useState(false);
  if (!editMode && deleteConfirm) setDelete(false);
  const toRemove = () => setDelete(true);
  const remove = () => {
    const { room, id } = props;
    props.actions.removeItem(room, id);
  };
  return (
    <Div {...props}>
      {editMode && !deleteConfirm && (
        <img src={minusIcon} alt="-" onClick={toRemove} />
      )}
      <div className="info">
        <h4>
          {type} - {material}
        </h4>
        <h5>
          {width} X {depth}
        </h5>
      </div>
      {!editMode && <span className="icon-seta" />}
      {deleteConfirm && <DeleteButton onClick={remove} />}
    </Div>
  );
};

export default ProductInfo;
