import React from "react";
import ListItem from "./ListItem";

const LinkList = props => (
  <ul {...props}>
    <ListItem to="/" icon="home" label="HOME" />
    <ListItem to="/orders" icon="pedido" label="PEDIDOS" />
    <ListItem to="/budgets" icon="orcamento" label="ORÇAMENTOS" />
    <ListItem to="/clients" icon="clientes" label="CLIENTES" />
    <ListItem to="/receipts" icon="pontuacao" label="PONTUAÇÃO" />
  </ul>
);

export default LinkList;
