import React from "react";
import Title from "components/common/Auth/Title";
import FormContainer from "./FormContainer";
import Button from "components/common/Auth/Button";

const JobForm = ({ update }) => {
  const updateVal = ev => update(ev, { type_id: ev.target.name });

  return (
    <>
      <Title>Sou</Title>
      <FormContainer>
        <Button onClick={updateVal} name="1">
          Arquiteto
        </Button>
        <Button onClick={updateVal} name="2">
          Projetista de Móveis
        </Button>
        <Button onClick={updateVal} name="4">
          Designer de Interiores
        </Button>
        <Button onClick={updateVal} name="3">
          Consumidor
        </Button>
      </FormContainer>
    </>
  );
};

export default JobForm;
