import React from "react";
import CardContainer from "./card.styles";
import arrowIcon from "assets/icons/arrow.png";
import getStatusFromColor from "Utils/getStatusFromColor";

const Card1 = ({ statusColor, id, status }) => (
  <CardContainer bg={statusColor} to={`/budgets/${id}`}>
    <div className="topBar" />
    <dl>
      <dt>ORÇAMENTO</dt>
      <dd>{id}</dd>

      <dt>STATUS</dt>
      <dd>{getStatusFromColor(statusColor)}</dd>
      <span title={status}>
        <b>{status}</b>
        <img src={arrowIcon} alt="->" />
      </span>
    </dl>
  </CardContainer>
);

export default Card1;
