import React from "react";
import styled from "styled-components";
import OptionLink from "./OptionLink";
import help_icon1 from "assets/icons/help_icon1.png";
import help_icon2 from "assets/icons/help_icon2.png";
import help_icon3 from "assets/icons/help_icon3.png";

const Div = styled.div`
  width: 100%;
  max-width: 886px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media (max-width: 768px) {
    flex-direction: column;
  }
`;

const Help = () => (
  <Div>
    <OptionLink
      to="#"
      title="Como criar seu próprio orçamento"
      bg="#1E335E"
      icon={help_icon1}
    />
    <OptionLink
      to="#"
      title="Como criar seu próprio orçamento"
      bg="#2AC940"
      icon={help_icon2}
    />
    <OptionLink
      to="/faq"
      title="Como criar seu próprio orçamento"
      bg="#F3CA3E"
      icon={help_icon3}
    />
  </Div>
);

export default Help;
