import React from "react";
import { Redirect } from "react-router-dom";
import Container from "components/common/Container";
import { userContext } from "contexts/userContext";
import Hider from "components/Hider";
import { getOrderInfo, GenerateOrder } from "Utils/Connect";
import TopInfo from "./TopInfo";
import List from "./List";
import SumInfo from "./SumInfo";
import ButtonsContainer from "components/common/ButtonsContainer";

class Budget extends React.Component {
  state = { loading: true };

  static contextType = userContext;

  async componentDidMount() {
    const response = await getOrderInfo(this.props.match.params.id, this.context.access_token);
    if (!response) return this.setState({ notFound: true });
    this.setState({ loading: false, ...response });
  }

  generate = async () => {
    const id = this.props.match.params.id;
    await GenerateOrder(id, this.context.access_token);
    this.props.history.push(`/orders/${id}`);
  };

  actions = {
    duplicate: () => {
      console.log("duplicate");
    },
    removeList: name => {
      const { products } = { ...this.state };
      delete products[name];
      this.setState({ products });
    },
    removeItem: (name, id) => {
      const { products } = { ...this.state };
      products[name] = products[name].filter(m => m.id !== id);
      this.setState({ products });
    }
  };

  render() {
    if (this.state.notFound)
      return <Redirect to="/notFound?title=Não encontramos o identificador de order" />;

    if (this.state.loading) return <Hider />;

    const { order, products } = this.state;

    return (
      <Container>
        <TopInfo {...order} />
        {Object.keys(products).map(k => (
          <List key={k} name={k} data={products[k]} actions={this.actions} />
        ))}
        <SumInfo order={order} />
        <ButtonsContainer
          mode={order.isOrder ? 0 : 2}
          actionName="GERAR PEDIDO"
          Action={this.generate}
        />
      </Container>
    );
  }
}

export default Budget;
