import styled from "styled-components";

const ItemTitle = styled.h3`
  font-size: 20px;
  font-weight: 700;
  line-height: 22px;
  margin-bottom: 10px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  @media (max-width: 768px) {
    font-size: 15px;
    font-weight: 700;
    line-height: 17px;
  }
`;

export default ItemTitle;
