import React from "react";
import styled from "styled-components";
import StyledPicker from "components/common/Forms/StyledPicker";

const Div = styled.div`
  display: flex;
  width: 100%;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  padding: 0 20px;
  margin-top: 15px;
  @media (max-width: 768px) {
    padding: 0;
  }
`;

const BudgetTiming = ({ update, date, time }) => {
  const handleChange = ({ target }) => update({ [target.name]: target.value });

  return (
    <Div>
      <StyledPicker
        name="date"
        value={date}
        label="DATA"
        onChange={handleChange}
        w="2"
        required
      />
      <StyledPicker
        name="time"
        value={time}
        label="HORÁRIO"
        onChange={handleChange}
        w="2"
        required
        showTimeSelect
        showTimeSelectOnly
        timeIntervals={30}
        dateFormat="HH:mm"
        timeCaption="HORÁRIO"
      />
    </Div>
  );
};

export default BudgetTiming;
