import React from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";

const Li = styled.li`
  width: 140px;
  margin-top: 20px;
  white-space: nowrap;
  &:first-child {
    margin-top: 0;
  }
  a {
    display: flex;
    align-items: center;
    color: ${({ theme }) => theme.text};
    transition: none;
  }
  a:hover {
    color: ${({ theme }) => theme.textAlt};
  }
  span {
    font-size: 26px;
  }
  b {
    font-size: 16px;
    font-weight: 400;
    margin-left: 15px;
    line-height: 17px;
  }
`;

const MenuLink = ({ to, icon, label, ...props }) => (
  <Li>
    <Link to={to} {...props}>
      <span className={`icon-${icon}`} />
      <b>{label}</b>
    </Link>
  </Li>
);

export default MenuLink;
