import React from "react";
import styled from "styled-components";
import FormatStatusHistory from "Utils/FormatStatusHistory";
import ProgressInfo from "./ProgressInfo";
import ProgressBar from "./ProgressBar";

const Div = styled.div`
  width: 100%;
  margin-top: 25px;
  > div {
    display: flex;
    justify-content: center;
    align-items: flex-start;
  }
  @media (max-width: 768px) {
    display: block;
    width: 250px;
    position: relative;
    > div {
      display: block;
    }
  }
`;

const ProgressTracker = ({ info }) => {
  const data = FormatStatusHistory(info);

  return (
    <Div>
      <div>
        {data.map(status => (
          <ProgressInfo {...status} key={status.label} />
        ))}
      </div>
      <ProgressBar data={data} />
    </Div>
  );
};

export default ProgressTracker;
