import React from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";
import doneIcon from "assets/icons/done.png";

const Div = styled.div`
  min-height: 400px;
  width: 100%;
  max-width: 600px;
  border-radius: 5px;
  background-color: ${({ theme }) => theme.bodyBg};
  box-shadow: 2px 2px 20px 0 ${({ theme }) => theme.shadow};
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  padding: 30px;
  font-size: 20px;
  font-weight: 300;
  line-height: 21px;
  * {
    font: inherit;
    color: inherit;
    text-align: center;
  }
  color: ${({ theme }) => theme.green};
  h1 {
    font-size: 30px;
    font-weight: 700;
    line-height: 33px;
    margin: 20px 0;
  }
  b {
    color: ${({ theme }) => theme.text};
    margin-top: 15px;
  }
  a {
    color: ${({ theme }) => theme.white};
    padding: 12px 20px;
    border-radius: 8px;
    margin-top: 60px;
    background: ${({ theme }) => theme.gradient(58)};
  }
  @media (max-width: 768px) {
    background-color: ${({ theme }) => theme.bodyBgMobile};
    box-shadow: none;
    padding-top: 15vh;
    font-size: 15px;
    line-height: 16px;
    h1 {
      font-size: 20px;
    }
  }
`;

const Done = ({ title, text, subText, to }) => (
  <Div>
    <img src={doneIcon} alt="done" />
    <h1>{title}</h1>
    <p>{text}</p>
    {subText && <b>{subText}</b>}
    <Link to={to}>IR PARA PÁGINA PRINCIPAL</Link>
  </Div>
);

export default Done;
