import { createGlobalStyle } from "styled-components";

const GlobalStyle = createGlobalStyle`
  * {
    margin: 0;
    padding: 0;
    font-family: gotham, "Arial", "Helvetica Neue", "Helvetica", sans-serif;
    box-sizing: border-box;
    text-decoration: none;
    transition: all 0.3s ease-in-out 0s, color 0s, font-weight 0s;
    vertical-align: middle;
    text-rendering: optimizeLegibility;
    -moz-font-smoothing: antialiased;
    -webkit-font-smoothing: antialiased;
    color: ${({ theme }) => theme.text};
    border: 0;
    background: transparent;
  }
  *:focus{
    outline: 0;
  }
  *:invalid{
    color: ${({ theme }) => theme.error};
    border-color: ${({ theme }) => theme.error};
  }

  li,
  ul {
    list-style: none;
  }

  body {
    background: ${({ theme }) => theme.bodyBg};
    max-width: 100%;
    overflow-x: hidden;
  }

  ::selection {
    color: ${({ theme }) => theme.textAlt};
    background: ${({ theme }) => theme.bgAlt};
  }

  body::-webkit-scrollbar-track,
  .customScrollbars::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px ${({ theme }) => theme.shadowLight};
    background-color: ${({ theme }) => theme.bgAlt};
    border-radius: 10px;
  }

  body::-webkit-scrollbar,
  .customScrollbars::-webkit-scrollbar {
    width: 10px;
    background-color: ${({ theme }) => theme.bgAlt};
  }

  body::-webkit-scrollbar-thumb,
  .customScrollbars::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-image: -webkit-gradient(
      linear,
      left bottom,
      left top,
      color-stop(0, ${({ theme }) => theme.accent}),
      color-stop(1, ${({ theme }) => theme.primary})
    );
  }

  svg {
    fill: currentcolor;
    stroke: currentcolor;
  }
  
  @media (max-width: 768px){
    body {
      background: ${({ theme }) => theme.bodyBgMobile};
    }
  }
`;

export default GlobalStyle;
