import React, { useState } from "react";
import { BrowserRouter } from "react-router-dom";
import { ThemeProvider } from "styled-components";
import { UserContextProvider } from "contexts/userContext";
import Router from "router";
import GlobalStyle from "./GlobalStyle";
import Themes from "./Themes/themes";

const getDefaultTheme = (() => {
  const storedTheme = sessionStorage.getItem("theme");
  if (storedTheme in Themes) return Themes[storedTheme];
  const matchSystem = theme =>
    window.matchMedia && window.matchMedia(`(prefers-color-scheme: ${theme})`).matches;

  if (matchSystem("dark")) return Themes["dark"];
  if (matchSystem("light")) return Themes["light"];

  //  Match by time if there is no information from system
  const hours = new Date().getHours();
  return hours > 6 && hours < 20 ? Themes["light"] : Themes["dark"];
})();

const App = () => {
  const [theme, setTheme] = useState(getDefaultTheme);

  const switchTheme = t => {
    if (!t) t = theme.name === "light" ? "dark" : "light";
    if (!(t in Themes)) return null;
    sessionStorage.setItem("theme", Themes[t].name);
    setTheme(Themes[t]);
  };

  return (
    <ThemeProvider theme={{ ...theme, switchTheme }}>
      <UserContextProvider>
        <GlobalStyle />
        <BrowserRouter>
          <Router />
        </BrowserRouter>
      </UserContextProvider>
    </ThemeProvider>
  );
};

export default App;
