import React from "react";
import StyledSelect from "../StyledSelect";
import useFetch from "Utils/useFetch";
import { server_url } from "Utils/Connect";

const StateSelect = ({ update, state }) => {
  const updateSelected = ({ value, id }) =>
    update({
      state: value,
      state_id: id,
      city: undefined,
      city_id: undefined,
      neighborhood: undefined,
      neighborhood_id: undefined
    });

  const [data, loading] = useFetch(`${server_url}api/get-state-list`, {
    method: "POST"
  });

  const options = data
    ? data.map(state => ({
        ...state,
        label: state.name,
        value: state.name
      }))
    : [];

  const value = state ? { label: state, value: state } : null;

  return (
    <StyledSelect
      noOptionsMessage={() => (loading ? "Carregando..." : "nada encontrado")}
      options={options}
      loading={loading}
      placeholder="ESTADO"
      updateSelected={updateSelected}
      required
      value={value}
    />
  );
};

export default StateSelect;
