import styled from "styled-components";

const StatusIndicator = styled.span`
  display: block;
  height: 60px;
  min-width: 18px;
  max-width: 18px;
  margin-left: 10px;
  border-radius: 2px;
  background: ${({ color }) => color};
  @media (max-width: 768px) {
    height: 50px;
    min-width: 10px;
    max-width: 10px;
    margin-left: 7px;
  }
`;

export default StatusIndicator;
