import React from "react";
import routes from "routes";
import { Switch, Route, Redirect } from "react-router-dom";
import Header from "components/Header";
import Layout from "components/Layout/Layout";
import Menu from "components/Menu";
import Auth from "components/Auth";
import NotFound from "components/NotFound";
import Footer from "components/Footer";
import RouteUpdates from "RouteUpdates";
import { MenusContextProvider } from "contexts/menusContext";

const makeComponent = ({ auth, Component, title }) =>
  auth
    ? props => (
        <Auth>
          <RouteUpdates location={props.location} routeTitle={title} />
          <MenusContextProvider>
            <Header routeTitle={title} />
            <Menu />
          </MenusContextProvider>
          <Layout>
            <Component {...props} />
          </Layout>
          <Footer />
        </Auth>
      )
    : props => <Component {...props} />;

const Router = () => (
  <Switch>
    {routes.map((route, index) => (
      <Route exact path={route.path} render={makeComponent(route)} key={index} />
    ))}
    <Redirect from="/new_budget" to="/new_budget/address" />
    <Route render={makeComponent({ auth: true, Component: NotFound })} />
  </Switch>
);

export default Router;
