import React from "react";
import styled from "styled-components";
import ccImg from "assets/icons/credit-card-white.png";
import linkIcon from "assets/icons/link-2.png";
import Total from "./Total";
import ActionLink from "./ActionLink";

const Div = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  width: 100%;
  .links {
    flex-grow: 1;
  }
  @media (max-width: 768px) {
    display: block;
  }
`;

const Actions = ({ cashed, ...order }) => (
  <Div>
    <Total {...order} cashed={cashed} />
    <div className="links">
      <ActionLink to="./method" icon={ccImg} label="FAZER PAGAMENTO" />
      <ActionLink
        to="./send_link"
        icon={linkIcon}
        label="ENVIAR LINK DE PAGAMENTO"
      />
    </div>
  </Div>
);

export default Actions;
