import React, { Component } from "react";
// import { login } from "Utils/Connect";
import TextInput from "components/common/Auth/TextInput";
import Button from "components/common/Auth/Button";

class ResetCodeForm extends Component {
  state = {
    wrongAuth: false,
    code: ""
  };

  handleChange = ev => this.setState({ [ev.target.name]: ev.target.value });

  handleSubmit = async ev => {
    ev.preventDefault();
    console.log(this.state.code);
  };

  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        <TextInput
          type="text"
          placeholder="Código de verificação"
          autoComplete="off"
          required
          value={this.state.code}
          name="code"
          onChange={this.handleChange}
          mask="S- 999-999"
          pattern="S- \d{3}-\d{3}"
        />
        <Button type="submit">Recuperar Senha</Button>
      </form>
    );
  }
}

export default ResetCodeForm;
