import React from "react";
import Info from "./Info";
import creditCardIcon from "assets/icons/credit-card.png";
import carIcon from "assets/icons/car.png";

const InfoLabels = ({ order, id }) => (
  <>
    <Info
      price={order.price}
      to={`/budgets/${id}`}
      statusText="VER ITENS"
      arrow
    />
    <Info
      icon={creditCardIcon}
      title="PAGAMENTO"
      to={`/payments/${id}`}
      statusText="VER ITENS"
      arrow
    />
    <Info icon={carIcon} title="ENTREGA" statusText={order.status} />
  </>
);

export default InfoLabels;
