export const GetBudgetsOrdersList = async token => {
  const url = `${server_url}api/order/list`;
  try {
    const response = await fetch(url, {
      method: "POST",
      credentials: "include",
      redirect: "manual",
      headers: new Headers({
        Authorization: `Bearer ${token}`
      })
    });
    return await response.json();
  } catch (error) {
    console.warn(`Error: ${error}`);
    return null;
  }
};

export const GetHomeInfo = async token => {
  const response = await GetBudgetsOrdersList(token);
  if (response && response.estimates && response.orders) {
    const { estimates, orders } = response;
    return {
      budgets: [...estimates.slice(0, 3), { special: true }],
      orders: orders.slice(0, 4)
    };
  }
  return null;
};

export const getBudgets = async token => {
  const response = await GetBudgetsOrdersList(token);
  if (response && response.estimates) return response.estimates;
  return null;
};

export const getOrders = async token => {
  const response = await GetBudgetsOrdersList(token);
  if (!response || !response.orders) return false;
  return response.orders.map(order => ({
    ...order,
    completed: isCompleted(order.statusColor)
  }));
};

export const getOrderInfo = async (order_id, token) => {
  const url = `${server_url}api/order/products`;
  try {
    const response = await fetch(url, {
      method: "POST",
      headers: new Headers({
        Authorization: `Bearer ${token}`,
        "Content-Type": "application/json"
      }),
      body: JSON.stringify({ order_id })
    });
    if (!response.ok) return null;
    return await response.json();
  } catch (error) {
    console.warn(`Error: ${error}`);
    return null;
  }
};

