// Temporary Requests

export const getVisits = async () => {
  const url = `${server_url}_api/visits.json`;

  try {
    const response = await fetch(url);
    const data = await response.json();
    const toFilter = JSON.parse(sessionStorage.getItem("deletedVisits")) || [];
    return data.filter(({ id }) => !toFilter.includes(id));
  } catch (error) {
    console.warn(`Request Error: ${error}`);
    return null;
  }
};

export const getLatestVisits = async () => {
  let visits = await getVisits();
  return visits.slice(0, 2);
};

