import React from "react";
import Container from "components/common/Container";
import mapIcon from "assets/icons/map.png";
import Choices from "components/common/Choices";
import AddressForm from "components/common/AddressForm/AddressForm";
import ButtonsContainer from "components/common/ButtonsContainer";
import ErrorMsg from "components/common/Auth/ErrorMsg";
import validateAddress from "Utils/validateAddress";
import { userContext } from "contexts/userContext";
import { Redirect } from "react-router-dom";

class NewBudgetAddress extends React.Component {
  state = {
    mode: 0,
    error: null
  };

  static contextType = userContext;

  update = obj => {
    if (obj.mode === 1) {
      return this.setState({ use_owner_address: true }, () => this.submit());
    }
    this.setState({ ...obj, error: null, use_owner_address: false });
  };

  submit = async ev => {
    if (ev) ev.preventDefault();
    let { mode, ...data } = this.state;
    if (!data.use_owner_address) {
      const validationResult = await validateAddress(data);
      if (typeof validationResult === "string") return this.setState({ error: validationResult });
      data = { ...validationResult };
    }
    this.props.history.push("/new_budget/owner", data);
  };

  render() {
    if (!this.context.hasValidAddress) return <Redirect to="/profile#fixAddress" />;
    return (
      <Container icon={mapIcon} title="ENDEREÇO DE ENTREGA">
        <Choices
          choices={["MEU ENDEREÇO", "ENDEREÇO DA OBRA"]}
          update={this.update}
          mode={this.state.mode}
        />
        <ErrorMsg msg={this.state.error} />
        {this.state.mode === 2 && (
          <form id="new_budget_address" onSubmit={this.submit}>
            <AddressForm update={this.update} required {...this.state} />
          </form>
        )}
        <ButtonsContainer mode={this.state.mode} actionName="AVANÇAR" formId="new_budget_address" />
      </Container>
    );
  }
}

export default NewBudgetAddress;
