import React from "react";
import styled from "styled-components";
import ProductInfo from "./ProductInfo";
import PriceInfo from "./PriceInfo";

const Div = styled.div``;

const ListItem = props => {
  const { editMode } = props;
  let [priceOpen, setPriceOpen] = React.useState(false);
  const togglePrice = () => !editMode && setPriceOpen(!priceOpen);
  if (editMode && priceOpen) setPriceOpen(false);

  return (
    <Div>
      <ProductInfo {...props} onClick={togglePrice} priceOpen={priceOpen} />
      {priceOpen && <PriceInfo {...props} />}
    </Div>
  );
};

export default ListItem;
