import React from "react";
import StyledSelect from "../StyledSelect";
import useFetch from "Utils/useFetch";
import { server_url } from "Utils/Connect";

const NeighborhoodSelect = ({ update, city_id, neighborhood }) => {
  const updateSelected = ({ value, id }) =>
    update({ neighborhood: value, neighborhood_id: id });

  const [data, loading] = useFetch(
    `${server_url}api/get-neighborhood-list`,
    {
      method: "POST",
      headers: new Headers({
        Accept: "application/json, text/plain, */*",
        "Content-Type": "application/json",
        "Access-Control-Allow-Credentials": true
      }),
      body: JSON.stringify({ city_id })
    },
    city_id
  );

  const options = data
    ? data.map(neighborhood => ({
        ...neighborhood,
        label: neighborhood.name,
        value: neighborhood.name
      }))
    : [];

  const value = neighborhood
    ? { label: neighborhood, value: neighborhood }
    : null;

  return (
    <StyledSelect
      noOptionsMessage={() =>
        loading
          ? "Carregando..."
          : city_id
          ? "nada encontrado"
          : "selecione uma Cidade"
      }
      options={options}
      loading={loading}
      placeholder="BAIRRO"
      updateSelected={updateSelected}
      required
      w={4}
      value={value}
    />
  );
};

export default NeighborhoodSelect;
