const getUrlParams = search => {
  let retVal = search.replace(/^\?/, "");
  if (retVal === "") return {};
  retVal = retVal.split("&").map(el => el.split("="));
  let obj = {};
  retVal.forEach(el => {
    obj[el[0]] = decodeURI(el[1]) || null;
  });
  return obj;
};

export default getUrlParams;
