const classifyBy = (array, key) =>
  array
    .sort((a, b) => a[key].localeCompare(b[key]))
    .reduce((r, a) => {
      const p = a[key][0];
      r[p] = r[p] || [];
      r[p].push(a);
      r[p] = r[p].sort((a, b) => a[key].localeCompare(b[key]));
      return r;
    }, {});

export default classifyBy;
