import styled from "styled-components";

export default styled.button`
  height: 48px;
  width: 315.5px;
  border-radius: 8px;
  background-color: ${({ theme }) => theme.white};
  box-shadow: 0 2px 20px 0 ${({ theme }) => theme.shadow};
  border: 0;
  color: ${({ theme }) => theme.primary};
  font-size: 20px;
  font-weight: 300;
  line-height: 21px;
  text-align: center;
  display: inline-block;
`;
