import React from "react";
import Title from "components/common/Auth/Title";
import Button from "components/common/Auth/Button";
import TextInput from "components/common/Auth/TextInput";
import ErrorMsg from "components/common/Auth/ErrorMsg";
import { CheckEmail } from "Utils/Connect";

class UserForm extends React.Component {
  state = {
    error: null,
    email: "",
    password: "",
    confirm_password: ""
  };

  handleChange = ev => this.setState({ [ev.target.name]: ev.target.value });

  handleSubmit = async ev => {
    ev.preventDefault();
    const { email, password, confirm_password } = this.state;

    if (password !== confirm_password)
      return this.setState({ error: "Senha incorreta" });

    const response = await CheckEmail(email);
    if (!response || response.found)
      return this.setState({ error: "Usuário já cadastrado" });

    return this.props.update(ev, { email, password }, true);
  };

  render() {
    return (
      <>
        <Title>Usuário</Title>
        {this.state.error ? <ErrorMsg msg={this.state.error} /> : null}
        <form onSubmit={this.handleSubmit}>
          <TextInput
            type="email"
            placeholder="Email"
            autoComplete="email"
            required
            value={this.state.email}
            name="email"
            onChange={this.handleChange}
          />
          <TextInput
            type="password"
            placeholder="Senha"
            autoComplete="new-password"
            minLength="8"
            required
            value={this.state.password}
            name="password"
            onChange={this.handleChange}
          />
          <TextInput
            type="password"
            placeholder="Confirmar senha"
            autoComplete="new-password"
            minLength="8"
            required
            value={this.state.confirm_password}
            name="confirm_password"
            onChange={this.handleChange}
          />
          <Button type="submit">Avançar</Button>
        </form>
      </>
    );
  }
}

export default UserForm;
