import React, { Component } from "react";
import { CheckEmail, ResetPassword } from "Utils/Connect";
import TextInput from "components/common/Auth/TextInput";
import Button from "components/common/Auth/Button";

class EmailForm extends Component {
  state = {
    wrongAuth: false,
    email: ""
  };

  handleChange = ev => this.setState({ [ev.target.name]: ev.target.value });

  handleSubmit = async ev => {
    ev.preventDefault();
    let response = await CheckEmail(this.state.email);
    if (response && response.found) {
      const success = await ResetPassword(this.state.email);
      if (!success) return this.props.setError("Um erro ocorreu");
      await this.props.setEmail(this.state.email);
      await this.props.setError(null);
    } else return this.props.setError("Email não encontrado");
  };

  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        <TextInput
          type="email"
          placeholder="Email cadastrado"
          autoComplete="email"
          required
          value={this.state.email}
          name="email"
          onChange={this.handleChange}
        />
        <Button type="submit">Receber Código</Button>
      </form>
    );
  }
}

export default EmailForm;
