import React from "react";
import styled from "styled-components";
import ListItem from "./ListItem";
import ClockIcon from "assets/icons/clock-fill.png";
import locationIcon from "assets/icons/pin-fill.png";
import commentIcon from "assets/icons/comment-fill.png";

const List = styled.ul`
  min-width: 0;
  flex-grow: 1;
  max-width: 60%;
  @media (max-width: 768px) {
    max-width: 100%;
    width: 100%;
  }
`;

const VisitInfo = ({ data: { day, date, time, address, title } }) => (
  <List>
    <ListItem icon={ClockIcon} text={`${day} - ${date} - ${time}`} />
    <ListItem icon={locationIcon} text={address} />
    <ListItem icon={commentIcon} text={title} />
  </List>
);

export default VisitInfo;
