import React from "react";
import styled from "styled-components";

const Div = styled.div`
  h4 {
    font-size: 20px;
    font-weight: 700;
    line-height: 22px;
    margin-bottom: 10px;
    margin-top: 5px;
  }
  p {
    font-size: 20px;
    font-weight: 300;
    line-height: 21px;
  }
  .icon-seta {
    font-size: 30px;
    display: none;
  }
  @media (max-width: 768px) {
    h4 {
      font-size: 18px;
    }
    p {
      font-size: 15px;
      line-height: 16px;
    }
    div {
      overflow: hidden;
      height: 0;
      touch-action: none;
    }
    .icon-seta {
      display: block;
    }
  }
`;

const OwnerInfo = ({ client, address }) => {
  const foldable = React.useRef(null);
  const icon = React.useRef(null);
  let startY = React.useRef(null);
  let ch = React.useRef(0);

  let touchStart = e => (startY.current = e.changedTouches[0].screenY);
  let touchEnd = () => (startY.current = null);
  let touchMove = e => {
    if (!foldable.current || !icon.current) return;
    let height = ch.current + e.changedTouches[0].screenY - startY.current;
    if (height < 20) height = 0;
    else if (height > 44) height = 64;
    foldable.current.style.height = height + "px";
    ch.current = height;
    let angle = (height / 64) * 180;
    icon.current.style.transform = `rotate(${angle}deg)`;
  };

  return (
    <Div
      onTouchStart={touchStart}
      onTouchMove={touchMove}
      onTouchEnd={touchEnd}
    >
      <h4>{client}</h4>
      <div ref={foldable}>
        {address.map((line, i) => (
          <p key={i}>{line}</p>
        ))}
      </div>
      <span className="icon-seta" ref={icon} />
    </Div>
  );
};

export default OwnerInfo;
