import React from "react";
import styled from "styled-components";
import VideoCard from "./VideoCard";
import videos from "./videos";
import ControlBtn from "./ControlBtn";

const Div = styled.div`
  position: relative;
  .videos {
    overflow-x: hidden;
    padding: 10px;
    margin: 0 -10px 10px -10px;
    > div {
      transform: translateX(
        calc(-${({ pos }) => pos * 50}% - ${({ pos }) => pos * 10}px)
      );
      width: fit-content;
      display: flex;
      justify-content: space-between;
      align-items: stretch;
    }
  }
  @media (max-width: 768px) {
    .videos {
      overflow-x: scroll;
      padding: 20px 10px;
      > div {
        transform: translateX(0);
      }
    }
  }
`;

const VideoList = () => {
  let [pos, setPos] = React.useState(0);
  const Previous = () => setPos(pos - 1);
  const Next = () => setPos(pos + 1);

  return (
    <Div pos={pos}>
      {pos > 0 && <ControlBtn onClick={Previous} />}
      <div className="videos">
        <div>
          {videos.map((vid, i) => (
            <VideoCard key={i} {...vid} />
          ))}
        </div>
      </div>
      {pos < videos.length - 2 && <ControlBtn next onClick={Next} />}
    </Div>
  );
};

export default VideoList;
