import styled from "styled-components";

const AvatarPreview = styled.span.attrs(({ mobile }) => ({
  className: "icon-usuario",
  dimension: mobile ? "50px" : "70px"
}))`
  height: ${({ dimension }) => dimension};
  width: ${({ dimension }) => dimension};
  margin-right: 20px;
  display: ${({ mobile }) => (mobile ? `none` : "block")};
  border-radius: 100%;
  overflow: hidden;
  background-size: cover;
  background-position: center;
  background-image: ${({ preview }) => (preview ? `url(${preview})` : "")};
  &:before {
    font-size: ${({ dimension }) => dimension};
    display: ${({ preview }) => (preview ? "none" : "block")};
  }
  @media (max-width: 768px) {
    display: ${({ mobile }) => (mobile ? `block` : "none")};
    margin: ${({ mobile }) => (mobile ? `0 auto` : "")};
  }
`;

export default AvatarPreview;
