import React from "react";
import styled from "styled-components";
import UserMenu from "./Menu/Menu";
import MenuShow from "./MenuShow";
import Notification from "./Notification";

const Div = styled.header`
  height: 90px;
  background-color: ${({ theme }) => theme.bg};
  box-shadow: 0 4px 10px 0 ${({ theme }) => theme.shadowLight};
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0 30px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
  h1 {
    display: none;
  }
  > div {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  @media (max-width: 768px) {
    color: ${({ theme }) => theme.text};
    p {
      width: 84px;
      height: 70px;
    }
    h1 {
      display: flex;
      align-items: center;
      b {
        color: ${({ theme }) => theme.text};
        font-size: 15px;
        font-weight: 300;
        line-height: 16px;
      }
    }
    justify-content: space-between;
    height: 70px;
    padding: 0 10px;
    background: ${({ theme }) => theme.bgAlt};
  }
`;

const Header = ({ routeTitle }) => {
  return (
    <Div>
      <p />
      <h1>
        <b>{routeTitle}</b>
      </h1>
      <div>
        <Notification />
        <MenuShow />
        <UserMenu />
      </div>
    </Div>
  );
};

export default Header;
