import React, { useState, useContext } from "react";
import styled from "styled-components";
import NotificationsList from "./NotificationsList";
import notificationPlaceholder from "assets/icons/notification_placeholder.png";
import notificationPlaceholder2 from "assets/icons/notification_placeholder2.png";
import { menusContext } from "contexts/menusContext";
import { BellIcon } from "components/Icons";

const notifications = [
  {
    link: "/orders",
    text: "Novo orçamento criado",
    time: "3 minutos atrás",
    img: notificationPlaceholder
  },
  {
    link: "/budgets",
    text: "Novo pedido entregue com sucesso",
    time: "1 hora atrás",
    img: notificationPlaceholder2
  },
  {
    link: "/orders",
    text: "Cálculo de orçamento feito",
    time: "4 horas atrás",
    img: notificationPlaceholder
  }
];

const Div = styled.div`
  margin-right: 20px;
  position: relative;

  svg {
    cursor: pointer;
    font-size: 24px;
  }

  ::after {
    content: " ";
    z-index: 12;
    display: ${({ newNotification }) => (newNotification ? "block" : "none")};
    position: absolute;
    top: -2px;
    right: -1px;
    background: ${({ theme }) => theme.gradient(58)};
    width: 10px;
    height: 10px;
    border-radius: 100%;
    border: solid 2px ${({ theme }) => theme.bg};
  }
`;

const Notification = () => {
  let [newNotification, setNewNotification] = useState(notifications.length > 0);
  const { dispatch, showNotificationMenu } = useContext(menusContext);

  const toggleNotification = () => {
    newNotification === true && setNewNotification(false);
    dispatch({
      type: "TOGGLE",
      menu: "Notification"
    });
  };

  return (
    <Div newNotification={newNotification}>
      <BellIcon onClick={toggleNotification} />
      {showNotificationMenu && <NotificationsList notifications={notifications} />}
    </Div>
  );
};

export default Notification;
