import React from "react";
import styled from "styled-components";
import FilterItem from "./FilterItem";

const Div = styled.div`
  position: absolute;
  top: 47px;
  left: -1px;
  right: -1px;
  border-radius: 0px 0px 5px 5px;
  z-index: 9;
  overflow: hidden;
  background-color: ${({ theme }) => theme.bg};
  border: 1px solid ${({ theme }) => theme.border};
  border-top: 0;
  hr {
    border: 0;
    border-top: solid 1px ${({ theme }) => theme.border};
    margin: 0 15px;
  }
  li:hover {
    background: ${({ theme }) => theme.bgAlt};
  }
  @media (max-width: 768px) {
    background: transparent;
    position: static;
    border: 0;
    overflow: initial;
    flex-grow: 1;
    min-width: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    hr {
      display: none;
    }
  }
`;

const FilterOptions = ({ updateFilter, ...props }) => (
  <Div {...props}>
    <hr />
    <FilterItem onClick={() => updateFilter(null)}>TODOS</FilterItem>
    <FilterItem onClick={() => updateFilter(true)}>PENDENTES</FilterItem>
    <FilterItem onClick={() => updateFilter(false)}>CONCLUÍDOS</FilterItem>
  </Div>
);

export default FilterOptions;
