import React from "react";
import styled from "styled-components";
import InputMask from "react-input-mask";

const focusedLabel = `font-size: 10px;line-height: 10px;`;
const Label = styled.label`
  margin-bottom: 16px;
  border-radius: 5px;
  border: 1px solid ${({ theme }) => theme.border};
  width: ${({ width }) => width};
  height: 50px;
  position: relative;
  font-size: 20px;
  font-weight: 300;
  line-height: 40px;
  padding: 5px 15px;
  overflow: hidden;
  b {
    font: inherit;
    padding: 0 15px;
    position: absolute;
    left: 0;
    ${({ value }) => value && focusedLabel}
  }
  input:focus + b {
    ${focusedLabel}
  }
  input {
    font: inherit;
    width: 100%;
  }
  @media (max-width: 768px) {
    width: ${({ widthM }) => widthM};
    height: 40px;
    line-height: 34px;
    font-size: 17px;
    padding: 3px 15px;
  }
`;

const FormInput = ({ label, w, wM, value, ...props }) => {
  const calcWidth = w => {
    const values = [0, 35, 49, 63, 100];
    return (values[parseInt(w)] || values[1]) + "%";
  };

  return (
    <Label width={calcWidth(w)} widthM={calcWidth(wM || w)} value={value}>
      <InputMask {...props} value={value} />
      <b>{label}</b>
    </Label>
  );
};

export default FormInput;
