import { GetNeighborhoodsInCity, GetCitiesInState, FindStateInfo, GetCEPInfo } from "./Connect";

const validateAddress = async data => {
  const newData = { ...data };

  // CEP Validation
  if (!data.cep) return "CEP inválido";
  const cepRes = await GetCEPInfo(data.cep.replace("-", ""));
  if (!cepRes || cepRes.erro) return "CEP inválido";
  newData.cep = cepRes.cep;

  // State Validation
  if (!data.state) return "ESTADO inválido";
  const stateRes = await FindStateInfo(data.state);
  let matchedState = stateRes.find(({ name }) => name.toLowerCase() === data.state.toLowerCase());
  if (!matchedState) return "ESTADO inválido";
  newData.state = matchedState.name;
  newData.state_id = matchedState.id;

  // City Validation
  if (!data.city) return "CIDADE inválido";
  const cityRes = await GetCitiesInState(matchedState.id);
  let matchedCity = cityRes.find(({ name }) => name.toLowerCase() === data.city.toLowerCase());
  if (!matchedCity) return "CIDADE inválido";
  newData.city = matchedCity.name;
  newData.city_id = matchedCity.id;

  // City and CEP match validation
  if (cepRes.ibge !== String(matchedCity.id)) return "CEP e CIDADE não corresponde";

  // Neighborhood Validation
  if (!data.neighborhood) return "BAIRRO inválido";
  const neighborhoodRes = await GetNeighborhoodsInCity(matchedCity.id);
  let matchedNeighborhood = neighborhoodRes.find(
    ({ name }) => name.toLowerCase() === data.neighborhood.toLowerCase()
  );
  if (!matchedNeighborhood) return "BAIRRO inválido";
  newData.neighborhood = matchedNeighborhood.name;

  return newData;
};

export default validateAddress;
