import React, { useContext } from "react";
import styled, { ThemeContext, createGlobalStyle } from "styled-components";
import Spinner from "react-spinner-material";

const Div = styled.div`
  position: fixed;
  top: 0;
  bottom: 0%;
  right: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: ${({ theme }) => theme.gradient(58)};
  z-index: 12;
  span {
    font-size: 100px;
    position: absolute;
    color: ${({ theme }) => theme.white};
  }
`;

const GlobalOverride = createGlobalStyle`body{overflow:hidden}`;

const Hider = () => {
  const { white } = useContext(ThemeContext);

  return (
    <Div>
      <Spinner size={180} spinnerColor={white} spinnerWidth={4} visible={true} />
      <GlobalOverride />
      <span className="icon-logo" />
    </Div>
  );
};

export default Hider;
