import React from "react";
import styled from "styled-components";
import formatMoney from "Utils/formatMoney";

const Div = styled.div`
  h4,
  h5 {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    span {
      text-align: center;
      display: block;
      width: 25%;
    }
  }
  h4 span {
    font-size: 15px;
    font-weight: 700;
    line-height: 17px;
    margin-bottom: 5px;
  }
  h5 span {
    font-size: 20px;
    font-weight: 300;
    line-height: 21px;
  }
  @media (max-width: 768px) {
    h4 span,
    h5 span {
      width: 35%;
    }
    h4 span:first-child,
    h5 span:first-child {
      width: 15%;
    }
    h4 span {
      font-size: 10px;
      font-weight: 700;
      line-height: 11px;
    }
    h5 span {
      font-size: 15px;
      font-weight: 300;
      line-height: 16px;
    }
  }
`;

const PriceInfo = props => (
  <Div>
    <h4>
      <span>QTD</span>
      <span>VALOR UNITÁRIO</span>
      <span>VALOR SERVIÇO</span>
      <span>VALOR TOTAL</span>
    </h4>
    <h5>
      <span>{props.quantity}</span>
      <span>
        {formatMoney(
          (props.price["SOMATÓRIO"] || 0) - (props.price["INSTALAÇÃO"] || 0)
        )}
      </span>
      <span>{formatMoney(props.price["INSTALAÇÃO"] || 0)}</span>
      <span>{formatMoney(props.price.FINAL)}</span>
    </h5>
  </Div>
);

export default PriceInfo;
