import React, { Component } from "react";
import Container from "components/common/Container";
import personIcon from "assets/icons/person.png";
import ActionButton from "components/common/ActionButton";
import { getClients } from "Utils/Connect";
import Hider from "components/Hider";
import ClientsFilter from "./ClientsFilter";
import classifyBy from "Utils/classifyBy";
import List from "./List";

class ClientsList extends Component {
  state = {
    data: null,
    filter: "",
    mode: 0
  };

  update = obj => this.setState(obj);
  async componentDidMount() {
    const data = await getClients();
    this.setState({ data });
  }

  filterData = () =>
    classifyBy(
      this.state.data.filter(client =>
        RegExp(this.state.filter, "i").test(client.name)
      ),
      "name"
    );

  deleteClient = id => {
    let data = [...this.state.data].filter(client => client.id !== id);
    this.setState({ data });
  };

  render() {
    if (this.state.data === null) return <Hider />;
    return (
      <Container icon={personIcon} title="SEUS CLIENTES">
        <ClientsFilter
          filter={this.state.filter}
          update={this.update}
          mode={this.state.mode}
        />
        <List
          data={this.filterData()}
          mode={this.state.mode}
          deleteClient={this.deleteClient}
        />
        <ActionButton to="/clients/new" text="NOVO CLIENTE" />
      </Container>
    );
  }
}

export default ClientsList;
