import styled from "styled-components";

const FilterItem = styled.li`
  padding: 0 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  font-size: 20px;
  font-weight: 300;
  line-height: 50px;
  b {
    font: inherit;
  }
  @media (max-width: 768px) {
    justify-content: center;
    text-align: center;
    height: 30px;
    width: 100px;
    border-radius: 5px;
    border: 1px solid ${({ theme }) => theme.border};
    padding: 0;
    text-align: center;
    line-height: 30px;
    font-size: 12px;
    font-weight: 300;
  }
`;

export default FilterItem;
