import React from "react";
import styled from "styled-components";
import OptionsMenu from "components/common/OptionsMenu";
import optionIcon from "assets/icons/icone_more.png";

const Div = styled.div`
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  input {
    padding: 0 15px;
    font-size: 18px;
    font-weight: 300;
    line-height: 50px;
    border: solid 1px ${({ theme }) => theme.border};
    border-radius: 5px;
    flex-grow: 1;
    min-width: 0;
  }
  input + div {
    margin-left: 20px;
  }
  > .icon-fechar,
  > img {
    cursor: pointer;
    padding: 5px;
    margin: 0 10px;
    display: block;
  }
  > img {
    transform: rotate(90deg);
  }
`;

const ClientsFilter = ({ filter, update, mode }) => {
  const change = ev => update({ filter: ev.target.value });

  return (
    <Div>
      <input
        type="text"
        onChange={change}
        value={filter}
        placeholder="BUSCAR"
      />
      {mode === 1 ? (
        <OptionsMenu
          update={update}
          close={() => update({ mode: 0 })}
          edit={() => update({ mode: 2 })}
          remove={() => update({ mode: 3 })}
        />
      ) : mode === 0 ? (
        <img src={optionIcon} alt="..." onClick={() => update({ mode: 1 })} />
      ) : (
        <span className="icon-fechar" onClick={() => update({ mode: 0 })} />
      )}
    </Div>
  );
};

export default ClientsFilter;
