import React, { useState } from "react";
import AuthWrapper from "components/common/Auth/AuthWrapper";
import StyledLink from "components/common/Auth/StyledLink";
import ButtonLink from "components/common/Auth/ButtonLink";
import Title from "components/common/Auth/Title";
import Paragraph from "components/common/Auth/Paragraph";
import EmailForm from "./EmailForm";
import ResetCodeForm from "./ResetCodeForm";
import ErrorMsg from "components/common/Auth/ErrorMsg";

const Reset = () => {
  document.title = `SANSTONE | REDEFINIR SENHA`;
  let [Email, setEmail] = useState(null);
  let [error, setError] = useState(null);

  const pContent = Email
    ? "Você receberá o código de recuperação no email cadastrado"
    : "Preencha com o email cadastrado para receber o código de recuperação";
  const linkContent = Email ? "Altere sua senha após acesso" : "Voltar";

  return (
    <AuthWrapper>
      <Title>Recuperação de Senha</Title>
      <Paragraph>{pContent}</Paragraph>
      {error ? <ErrorMsg msg={error} /> : null}
      {Email ? (
        <ResetCodeForm email={Email} />
      ) : (
        <EmailForm setEmail={setEmail} setError={setError} />
      )}
      <StyledLink to="/login">{linkContent}</StyledLink>
      <ButtonLink to="/login">login</ButtonLink>
    </AuthWrapper>
  );
};

export default Reset;
