import React from "react";
import styled from "styled-components";
import ActionButton from "components/common/ActionButton";
import uploadIcon from "assets/icons/cloud-upload.png";
import InfoTooltip from "./InfoTooltip";

const Div = styled.div`
  width: 100%;
  margin-top: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 15px;
  label {
    margin: 0 10px 0 0;
    input {
      display: none;
    }
  }

  @media (max-width: 768px) {
    flex-direction: column;
    label {
      font-size: 15px;
      padding: 10px 15px;
    }
  }
`;

const SendProject = ({ update }) => {
  const handleChange = e => update({ files: e.target.files });

  return (
    <Div>
      <ActionButton text="SELECIONAR ARQUIVO" icon={uploadIcon} grey drown>
        <input
          type="file"
          name="files"
          accept=".pdf,.jpg,.png"
          onChange={handleChange}
        />
      </ActionButton>
      <InfoTooltip msg="Arquivos permitidos: PNG, JPG, PDF." />
    </Div>
  );
};

export default SendProject;
