import styled from "styled-components";
import React from "react";
import { Link } from "react-router-dom";

const Li = styled.li`
  padding: 10px 0;
  a {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: ${({ theme }) => theme.white};
    font-size: 16px;
    font-weight: 300;
    line-height: 20px;
    span {
      font-size: 30px;
      margin-right: 10px;
      font-weight: 300;
      color: inherit;
    }
    b {
      font-weight: 300;
      color: inherit;
    }
  }
  a:hover {
    opacity: 0.9;
    transition: none;
  }
`;

const ListItem = ({ to, icon, label }) => (
  <Li>
    <Link to={to}>
      <span className={`icon-${icon}`} />
      <b>{label}</b>
    </Link>
  </Li>
);

export default ListItem;
