import styled from "styled-components";
import arrowIcon from "assets/icons/arrow.png";

const Arrow = styled.span`
  font-size: 42px;
  line-height: 1;
  font-family: "icomoon";
  content: "\e91b";
  transform: rotate(-90deg);
  @media (max-width: 768px) {
    transform: rotate(0deg);
    font-weight: 900;
    font-size: 30px;
    min-width: 30px;
    width: 30px;
    height: 30px;
    display: block;
    background: url(${arrowIcon}) center no-repeat / contain;
    &&::before {
      display: none;
    }
  }
`;

export default Arrow;
