import React, { useContext } from "react";
import styled, { ThemeContext } from "styled-components";
import ReactSelect from "react-select";

const Div = styled.div`
  width: ${({ width }) => width};
  margin-bottom: 15px;
  * {
    transition: 0s;
  }

  @media (max-width: 768px) {
    width: ${({ widthM }) => widthM};
  }
`;

const StyledSelect = ({ w, wM, updateSelected, ...props }) => {
  const theme = useContext(ThemeContext);

  const handleChange = selectedOption => updateSelected(selectedOption);

  const calcWidth = w => {
    const values = [0, 35, 49, 63, 100];
    return (values[parseInt(w)] || values[1]) + "%";
  };

  const selectorTheme = t => ({
    ...t,
    colors: {
      ...t.colors,
      text: theme.text,
      primary: theme.grey,
      primary25: theme.shadowLight,
      primary50: theme.accent,
      neutral0: theme.bg,
      neutral20: theme.border,
      neutral30: theme.primary,
      neutral50: theme.text,
      neutral70: theme.white,
      neutral80: theme.text
    }
  });

  const styles = {
    control: provided => ({
      ...provided,
      padding: "10px"
    })
  };

  return (
    <Div width={calcWidth(w)} widthM={calcWidth(wM || w)}>
      <ReactSelect
        onChange={handleChange}
        isSearchable={true}
        theme={selectorTheme}
        styles={styles}
        {...props}
      />
    </Div>
  );
};

export default StyledSelect;
