import React from "react";
import Container from "components/common/Container";
import SectionTitle from "components/common/SectionTitle";
import personIcon from "assets/icons/person.png";
import lockIcon from "assets/icons/lock.png";
import mapIcon from "assets/icons/map.png";
import chartIcon from "assets/icons/pie-chart.png";
import ActionButton from "components/common/ActionButton";
import AvatarEdit from "./AvatarEdit";
import { userContext } from "contexts/userContext";
import PersonalForm from "components/common/Forms/PersonalForm";
import SecurityForm from "components/common/Forms/SecurityForm";
import AddressForm from "components/common/AddressForm/AddressForm";
import BankForm from "components/common/Forms/BankForm";
import GenderSelect from "components/common/Forms/GenderSelect";
import Form from "components/common/Forms/Form";
import ErrorMsg from "components/common/Auth/ErrorMsg";

class Profile extends React.Component {
  static contextType = userContext;

  update = obj => this.setState(obj);

  submit = ev => {
    ev.preventDefault();
    console.log("Updated");
  };

  render() {
    for (let key in this.context) if (this.context[key] === null) this.context[key] = "";
    return (
      <Container>
        {window.location.hash === "#fixAddress" && (
          <ErrorMsg
            msg="Preencha as informações do endereço para criar um novo orçamento"
            style={{ marginBottom: "10px" }}
          />
        )}
        <Form onSubmit={this.submit}>
          <SectionTitle icon={personIcon} title="DADOS PESSOAIS" small />
          <AvatarEdit update={this.update} />
          <PersonalForm update={this.update} info={this.context} />
          <GenderSelect update={this.update} info={this.context} margin="-15px 0 30px 0" />

          <SectionTitle icon={lockIcon} title="DADOS DE ACESSO" small />
          <SecurityForm update={this.update} info={this.context} />

          <SectionTitle icon={mapIcon} title="ENDEREÇO" small />
          <AddressForm update={this.update} info={this.context} />

          <SectionTitle icon={chartIcon} title="DADOS DE RECEBIMENTO" small />
          <BankForm update={this.update} info={this.context} />

          <ActionButton type="submit" text="SALVAR" />
        </Form>
      </Container>
    );
  }
}

export default Profile;
