import React from "react";
import styled from "styled-components";
import { FacebookIcon, InstagramIcon, LinkedInIcon } from "components/Icons";

const Div = styled.div`
  display: flex;
  justify-content: flex-start;
  font-size: 24px;
  svg {
    fill: ${({ theme }) => theme.white};
  }
  a {
    margin: 0 15px;
  }
  @media (max-width: 768px) {
    justify-content: center;
    margin: 20px 0;
  }
`;

const SocialLinks = () => (
  <Div>
    <a href="https://www.facebook.com/sanstoneoficial/" target="_blank" rel="noopener noreferrer">
      <FacebookIcon />
    </a>
    <a href="https://www.instagram.com/sanstoneoficial/" target="_blank" rel="noopener noreferrer">
      <InstagramIcon />
    </a>
    <a href="https://www.linkedin.com/company/sanstone/" target="_blank" rel="noopener noreferrer">
      <LinkedInIcon />
    </a>
  </Div>
);

export default SocialLinks;
