const FormatStatusHistory = info => {
  let arr = [...Object.keys(info)];
  let arrOBj = arr.slice(0, 6).map(k => {
    let changes = Object.keys(info[k]).filter(c =>
      arr.slice(6, arr.length).includes(c)
    );
    if (changes[0]) {
      return {
        label: k,
        ...info[changes[0]],
        status: changes[0],
        done: true
      };
    } else return { label: k, done: false };
  });

  return arrOBj;
};

export default FormatStatusHistory;
