import React, { useState } from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import arrowIcon from "assets/icons/arrow.png";
import minusIcon from "assets/icons/minus-circle-fill.png";
import DeleteButton from "components/common/DeleteButton";

let StyledLink = styled(Link)`
  display: flex;
  height: 40px;
  padding: 0 15px;
  align-items: center;
  width: fit-content;
  &:not(a) {
    width: 100%;
  }
  h3 {
    flex-grow: 1;
    min-width: 0;
  }
  > img {
    margin-right: 10px;
    cursor: pointer;
  }
  @media (max-width: 768px) {
    padding: 0 5px;
    h3 {
      font-size: 15px;
      font-weight: 700;
      line-height: 17px;
    }
    > img {
      margin-right: 5px;
    }
  }
`;

const ClientItem = ({ client, mode, deleteClient }) => {
  StyledLink = StyledLink.withComponent((mode === 2 && Link) || "div");
  const [deleteConfirm, setDelete] = useState(false);
  if (mode !== 3 && deleteConfirm) setDelete(false);

  return (
    <StyledLink to={`/clients/${client.id}`} key={client.id}>
      {mode === 2 && <img src={arrowIcon} alt="->" />}
      {mode === 3 && !deleteConfirm && (
        <img src={minusIcon} alt="-" onClick={() => setDelete(true)} />
      )}
      <h3>{client.name}</h3>
      {deleteConfirm && (
        <DeleteButton onClick={() => deleteClient(client.id)} />
      )}
    </StyledLink>
  );
};

export default ClientItem;
