import React from "react";
import styled from "styled-components";
import editIcon from "assets/icons/icone_editar.png";
import duplicateIcon from "assets/icons/icone_duplicar.png";
import deleteIcon from "assets/icons/icone_excluir.png";

const Menu = styled.div`
  background: ${({ theme }) => theme.bgAlt};
  width: 150px;
  height: 50px;
  box-shadow: 2px 2px 10px 0 ${({ theme }) => theme.shadow};
  position: relative;
  span.icon-fechar {
    align-self: flex-end;
    cursor: pointer;
    display: block;
    width: 30px;
    line-height: 30px;
    margin: 10px;
    margin-left: auto;
    text-align: center;
  }
  ul {
    background: ${({ theme }) => theme.bgAlt};
    box-shadow: inherit;
    position: relative;
    position: absolute;
    top: 50px;
    left: 0;
    right: 0;
    z-index: 7;
  }
  ul:before {
    position: absolute;
    top: -10px;
    content: "";
    background: ${({ theme }) => theme.bgAlt};
    width: 100%;
    height: 10px;
  }
  li {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 50px;
    padding-left: 15px;
    cursor: pointer;
    b {
      font-size: 15px;
      font-weight: 300;
    }
    img {
      margin-right: 5px;
    }
  }
`;

const OptionsMenu = ({ close, edit, duplicate, remove }) => (
  <Menu>
    <span className="icon-fechar" onClick={close} />
    <ul>
      <li onClick={edit}>
        <img src={editIcon} alt="edit" />
        <b>EDITAR</b>
      </li>
      {duplicate && (
        <li onClick={duplicate}>
          <img src={duplicateIcon} alt="duplicate" />
          <b>DUPLICAR</b>
        </li>
      )}
      <li onClick={remove}>
        <img src={deleteIcon} alt="delete" />
        <b>EXCLUIR</b>
      </li>
    </ul>
  </Menu>
);

export default OptionsMenu;
