import styled from "styled-components";
import { Link } from "react-router-dom";

export default styled(Link)`
  background: ${({ theme }) => theme.primary};
  min-width: 250px;
  box-shadow: 2px 2px 20px 0 ${({ theme }) => theme.shadow};
  padding: 12.5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;

  svg {
    font-size: 102px;
    margin-bottom: 17px;
    fill: ${({ theme }) => theme.white};
    stroke-width: 0;
  }

  h3 {
    color: ${({ theme }) => theme.white};
    font-size: 25px;
    font-weight: 700;
    line-height: 28px;
    text-align: center;
  }

  &:hover {
    background: ${({ theme }) => theme.accent};
  }

  @media (max-width: 1100px) {
    min-width: 23vw;
    width: 23vw;
    margin: 0;
  }

  @media (max-width: 860px) {
    svg {
      width: 50%;
      height: auto;
    }

    h3 {
      font-size: 20px;
    }
  }

  @media (max-width: 768px) {
    box-shadow: 1px 1px 5px 0 ${({ theme }) => theme.shadowLight};
    border-radius: 5px;

    h3 {
      font-size: 18px;
    }
  }
  @media (max-width: 640px) {
    width: 60px;
    height: 60px;
    min-width: 60px;
    position: fixed;
    margin: 0;
    padding: 0;
    bottom: 20px;
    right: calc(50vw - 30px);
    border-radius: 100%;
    z-index: 10;
    overflow: hidden;
    box-shadow: 1px 1px 10px 0 ${({ theme }) => theme.shadowLight};
    text-indent: 2px;

    h3 {
      display: none;
    }

    svg {
      display: none;
    }

    &::before {
      content: "+";
      font-size: 50px;
      color: ${({ theme }) => theme.white};
      line-height: 60px;
    }
  }
`;
