import React from "react";
import styled from "styled-components";

const Div = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 105px;
  height: 15px;
  position: absolute;
  bottom: -50px;
`;

const Circle = styled.div`
  height: 15px;
  width: 15px;
  background-color: ${({ active, theme }) =>
    active ? theme.primary : theme.primaryTransparent};
  border-radius: 100%;
`;

const ProgressTrack = ({ progress }) => {
  const renderCircles = () => {
    let array = Array.from(new Array(4), () => "");
    return array.map((e, i) => <Circle active={progress === i} key={i} />);
  };

  return <Div progress={progress}>{renderCircles()}</Div>;
};

export default ProgressTrack;
