import React from "react";
import styled from "styled-components";
import formatMoney from "Utils/formatMoney";

const Div = styled.div`
  width: 100%;
  text-transform: uppercase;
  h3 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: ${({ theme }) => theme.bgAlt};
    padding: 10px 15px;
    font-size: 25px;
    font-weight: 300;
    margin-bottom: 15px;
    span {
      font: inherit;
    }
    b {
      font: inherit;
      font-weight: 700;
    }
  }
  u {
    display: block;
    font-size: 20px;
  }
  @media (max-width: 768px) {
    .noMobile {
      display: none;
    }
    h3,
    u {
      font-size: 10px;
    }
    h3 b {
      font-size: 15px;
    }
  }
`;

const TopInfo = ({ id, price, label }) => (
  <Div>
    <h3 className="noMobile">
      <span>PEDIDO</span>
      <b>{id}</b>
    </h3>
    <h3>
      <div>
        <u>TOTAL A PAGAR</u>
        <b>{formatMoney(price)}</b>
      </div>
      <span>{label}</span>
    </h3>
  </Div>
);

export default TopInfo;
