import React, { useState } from "react";
import validateCPF_CNPJ from "Utils/validateCPF_CNPJ";

const withDocMask = Component => ({ value, onChange, ...props }) => {
  let [isCNPJ, setCNPJ] = useState(false);

  const handleChange = ev => {
    let oldLength = value.replace(/\D/g, "").length;
    let newLength = ev.target.value.replace(/\D/g, "").length;

    let isNewCNPJ = (oldLength === 11 && newLength >= 11) || newLength > 11;
    if (isNewCNPJ !== isCNPJ) setCNPJ(isNewCNPJ);
    onChange(ev);
  };

  const inputHandler = ev => {
    const isValid = validateCPF_CNPJ(ev.target.value);
    ev.target.setCustomValidity(isValid.valid ? "" : "CPF / CNPJ INVÁLIDO");
  };

  return (
    <Component
      {...props}
      autoComplete="off"
      type="text"
      mask={isCNPJ ? "99.999.999/9999-99" : "999.999.999-999"}
      onChange={handleChange}
      maskChar={null}
      value={value}
      pattern="(^\d{3}\.\d{3}\.\d{3}-\d{2}$)|(^\d{2}\.\d{3}\.\d{3}\/\d{4}-\d{2}$)"
      onInput={inputHandler}
    />
  );
};

export default withDocMask;
