import React from "react";
import FormInput from "./FormInput";
import FormContainer from "./FormContainer";
import { personal, toObj } from "./config";
import withDocMask from "./withDocMask";
import StyledPicker from "./StyledPicker";

const DocNumberInput = withDocMask(FormInput);

class PersonalForm extends React.Component {
  state = { ...toObj(personal), ...this.props.info };
  handleChange = ev => {
    this.setState({ [ev.target.name]: ev.target.value });
    this.props.update({ [ev.target.name]: ev.target.value });
  };
  render() {
    const { required } = this.props;
    return (
      <FormContainer>
        <FormInput
          value={this.state[personal[0]]}
          type="text"
          name={personal[0]}
          label="NOME/RAZÃO SOCIAL"
          onChange={this.handleChange}
          w="4"
          required={required}
        />
        <DocNumberInput
          value={this.state[personal[1]]}
          name={personal[1]}
          onChange={this.handleChange}
          label="CPF(123.)/CNPJ(12.)"
          w="4"
          required={required}
        />
        <FormInput
          value={this.state[personal[2]]}
          type="tel"
          name={personal[2]}
          label="TELEFONE"
          onChange={this.handleChange}
          wM="2"
          mask="+(21) 99999-9999"
          pattern="\+\(21\) \d{5}-\d{3,4}"
          maskChar={null}
          required={required}
        />
        <StyledPicker
          name={personal[3]}
          value={this.state[personal[3]]}
          label="DATA DE NASCIMENTO"
          onChange={this.handleChange}
          w="3"
          wM="2"
          required
        />
      </FormContainer>
    );
  }
}

export default PersonalForm;
