import React from "react";
import { Redirect } from "react-router-dom";
import Container from "components/common/Container";
import TopInfo from "../TopInfo";
import { getOrderInfo } from "Utils/Connect";
import Hider from "components/Hider";
import { userContext } from "contexts/userContext";
import ButtonsContainer from "components/common/ButtonsContainer";
import H5 from "./H5";
import clockIcon from "assets/icons/clock.png";
import CodeCopy from "./CodeCopy";
import PrintButton from "./PrintButton";

class PayBoleto extends React.Component {
  state = { loading: true };
  id = this.props.match.params.id;

  static contextType = userContext;

  async componentDidMount() {
    const response = await getOrderInfo(this.id, this.context.access_token);
    if (!response) return this.setState({ notFound: true });
    this.setState({ loading: false, ...response });
  }
  done = () => this.props.history.push("./");
  render() {
    if (this.state.notFound)
      return <Redirect to="/notFound?title=Não encontramos o identificador de order" />;
    if (this.state.loading) return <Hider />;
    const { order } = this.state;
    return (
      <Container>
        <TopInfo {...order} label="BOLETO BANCÁRIO" />
        <H5>COPIE E COLE O CÓDIGO ABAIXO NO SEU INTERNET BANKING</H5>
        <CodeCopy />
        <H5>IMPRIMA O BOLETO PARA PAGAR EM UMA AGÊNCIA BANCÁRIA</H5>
        <PrintButton />
        <img src={clockIcon} alt="clock" />
        <H5>SEU PAGAMENTO SERÁ APROVADO EM ATÉ 3 DIAS ÚTEIS.</H5>
        <ButtonsContainer mode="2" actionName="PAGAR" Action={this.done} />
      </Container>
    );
  }
}

export default PayBoleto;
