import styled from "styled-components";
import { Link } from "react-router-dom";

export default styled(Link)`
  display: flex;
  text-decoration: none;
  min-height: 250px;
  min-width: 250px;
  max-width: 250px;
  text-align: left;
  margin-right: 30px;
  &:last-of-type {
    margin-right: 0;
  }
  background-color: ${({ theme }) => theme.bg};
  box-shadow: 2px 2px 20px 0 ${({ theme }) => theme.shadow};
  padding: 12px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  .topBar {
    width: 100%;
    background: ${prop => (prop.bg ? prop.bg : prop.theme.primary)};
    height: 31.25px;
  }
  dl {
    width: 100%;
    color: ${({ theme }) => theme.text};
    padding: 0 7.5px;
    dt,
    dd {
      font-size: 20px;
      font-weight: 300;
      line-height: 22px;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
    }
    dd {
      font-weight: 700;
      margin-bottom: 26.5px;
    }
    span {
      b {
        font-size: 12px;
        font-weight: 300;
        line-height: 13px;
      }
      img {
        display: none;
      }
      text-align: center;
      display: block;
      margin-bottom: 12px;
    }
  }

  @media (max-width: 1100px) {
    height: auto;
    min-width: 23vw;
    width: 23vw;
    margin: 0;
    dl span b {
      font-size: 13px;
      word-spacing: -1px;
      letter-spacing: -1px;
    }
  }
  @media (max-width: 860px) {
    min-height: auto;
    dl {
      padding: 10px 5px;
      position: relative;
      span {
        margin: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        img {
          display: block;
        }
        b {
          font-size: 12px;
          white-space: wrap;
          text-align: left;
        }
      }
      dt,
      dd {
        font-size: 16px;
      }
    }
  }
  @media (max-width: 768px) {
    box-shadow: 1px 1px 5px 0 ${({ theme }) => theme.shadow};
    border-radius: 5px;
    .topBar {
      height: 15px;
    }
  }
  @media (max-width: 640px) {
    width: 42vw;
    min-width: 42vw;
    margin-right: 15px;
    &:first-child {
      margin-left: 15px;
    }
    dl {
      span {
        font-size: 8px;
      }
      dd {
        margin-bottom: 10px;
        font-size: 10px;
        font-weight: 700;
        line-height: 11px;
      }
      dd:first-of-type {
        font-size: 15px;
      }
    }
  }
`;
