import React from "react";
import Title from "components/common/Auth/Title";
import FormContainer from "./FormContainer";
import Button from "components/common/Auth/Button";

const GenderForm = ({ update }) => {
  const updateVal = ev => update(ev, { gender: ev.target.name });

  return (
    <>
      <Title>Me considero</Title>
      <FormContainer>
        <Button onClick={updateVal} name="M">
          Homem
        </Button>
        <Button onClick={updateVal} name="F">
          Mulher
        </Button>
        <Button onClick={updateVal} name="O">
          Outro
        </Button>
      </FormContainer>
    </>
  );
};

export default GenderForm;
