import React from "react";
import styled from "styled-components";
import copyIcon from "assets/icons/icone_duplicar.png";
import checkIcon from "assets/icons/checkmark-circle-fill.png";

const Div = styled.div`
  padding: 10px 15px;
  border-radius: 5px;
  background-color: ${({ theme }) => theme.bgAlt};
  display: flex;
  justify-content: space-between;
  align-items: center;
  > div {
    strong {
      font-size: 20px;
      font-weight: 700;
      line-height: 22px;
    }
    u {
      font-size: 15px;
      font-weight: 300;
      line-height: 16px;
      margin-right: 15px;
      white-space: nowrap;
      color: ${({ theme, copied }) => (copied ? theme.green : theme.text)};
    }
  }
  @media (max-width: 768px) {
    flex-direction: column;
    > div {
      strong {
        font-size: 16px;
        line-height: 18px;
        text-align: center;
        display: block;
        margin-bottom: 5px;
      }
      u {
        font-size: 10px;
        line-height: 11px;
      }
    }
  }
`;

const CodeCopy = () => {
  const codeRef = React.useRef(null);
  let [copied, seCopied] = React.useState(false);

  const copyCode = () => {
    let textToCopy = codeRef.current.textContent;
    let input = document.createElement("input");
    input.style.opacity = 0;
    input.value = textToCopy;
    document.body.appendChild(input);
    input.select();
    document.execCommand("copy");
    seCopied(!copied);
  };

  return (
    <Div onClick={copyCode} copied={copied}>
      <div>
        <strong ref={codeRef}>
          23793.38029 60451.029791 48006.333305 1 79210000002000
        </strong>
      </div>
      <div>
        <u>{copied ? "CÓDIGO COPIADO" : "COPIAR CÓDIGO"}</u>
        <img src={copied ? checkIcon : copyIcon} alt="copy" />
      </div>
    </Div>
  );
};

export default CodeCopy;
