import React from "react";
import Container from "components/common/Container";
import fileIcon from "assets/icons/file-text.png";
import Filter from "components/common/Filter/index";
import ItemList from "components/common/ItemList";
import { getOrders } from "Utils/Connect";
import ActionButton from "components/common/ActionButton";
import Hider from "components/Hider";
import { userContext } from "contexts/userContext";
import Error from "components/Error";

class OrdersList extends React.Component {
  state = {
    data: null,
    filter: null
  };

  static contextType = userContext;

  async componentDidMount() {
    const data = await getOrders(this.context.access_token);
    this.setState(data ? { data } : { error: true });
  }

  filterData = () => this.state.data.filter(({ completed }) => completed !== this.state.filter);

  render() {
    if (this.state.error) return <Error msg="Algo deu errado, tente novamente alguns momentos" />;

    return this.state.data ? (
      <Container icon={fileIcon} title="SEUS PEDIDOS">
        <Filter setFilter={filter => this.setState({ filter })} />
        {this.state.data && <ItemList data={this.filterData()} dataType="order" />}
        <ActionButton to="/new_budget" text="NOVO ORÇAMENTO" />
      </Container>
    ) : (
      <Hider />
    );
  }
}

export default OrdersList;
