import React from "react";
import styled from "styled-components";

const Div = styled.div`
  position: absolute;
  top: calc(50% - 20px);
  cursor: pointer;
  ${({ next }) => (next ? "right" : "left")}: -50px;
  transform: rotate(${({ next }) => next && "-"}90deg);
  span {
    font-size: 40px;
    font-weight: 600;
  }
  @media (max-width: 768px) {
    display: none;
  }
`;

const ControlBtn = ({ next, ...props }) => (
  <Div next={next} {...props}>
    <span className="icon-seta" />
  </Div>
);

export default ControlBtn;
