import React from "react";
import { GetHomeInfo, getLatestVisits } from "Utils/Connect";
import CardList from "./CardList";
import VisitsList from "./VisitsList/VisitsList";
import Hider from "components/Hider";
import { userContext } from "contexts/userContext";
import Error from "components/Error";

class Home extends React.Component {
  state = {
    budgets: null,
    orders: null,
    visits: null
  };

  static contextType = userContext;

  async componentDidMount() {
    const [homeInfo, visits] = await Promise.all([
      GetHomeInfo(this.context.access_token),
      getLatestVisits()
    ]);
    if (!homeInfo) return this.setState({ error: true });
    this.setState({
      budgets: homeInfo.budgets,
      orders: homeInfo.orders,
      visits
    });
  }

  validateData = () => this.state.budgets && this.state.orders && this.state.orders;

  render() {
    if (this.state.error) return <Error msg="Algo deu errado, tente novamente alguns momentos" />;

    return this.validateData() ? (
      <div>
        <CardList cards={this.state.budgets} title="ÚLTIMOS ORÇAMENTOS" budgets="1" />
        <CardList cards={this.state.orders} title="SEUS PEDIDOS" />
        <VisitsList title="VISITAS AGENDADAS" visitsList={this.state.visits} />
      </div>
    ) : (
      <Hider />
    );
  }
}

export default Home;
