import React from "react";
import FormInput from "./FormInput";
import FormContainer from "./FormContainer";
import { personal, toObj } from "./config";

class NewOwnerForm extends React.Component {
  state = { ...toObj(personal), email: "" };
  handleChange = ev => {
    this.setState({ [ev.target.name]: ev.target.value });
    this.props.update({ [ev.target.name]: ev.target.value });
  };
  render() {
    return (
      <FormContainer>
        <FormInput
          value={this.state[personal[0]]}
          type="text"
          name={personal[0]}
          label="NOME/RAZÃO SOCIAL"
          onChange={this.handleChange}
          w="4"
          required
        />
        <FormInput
          value={this.state[personal[1]]}
          type="text"
          name={personal[1]}
          label="CPF/CNPJ"
          mask="999.999.999-99"
          pattern="\d{3}\.\d{3}\.\d{3}-\d\d"
          onChange={this.handleChange}
          w="4"
          required
        />
        <FormInput
          value={this.state.email}
          type="email"
          name="email"
          label="EMAIL"
          onChange={this.handleChange}
          w="4"
          required
        />
        <FormInput
          value={this.state[personal[2]]}
          type="tel"
          name={personal[2]}
          label="TELEFONE"
          onChange={this.handleChange}
          w="4"
          mask="+(21) 99999-9999"
          pattern="\+\(21\) \d{5}-\d{3,4}"
          maskChar={null}
          required
        />
      </FormContainer>
    );
  }
}

export default NewOwnerForm;
