import React from "react";
import styled from "styled-components";
import ActionButton from "./ActionButton";

const Div = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
`;

const ButtonsContainer = ({ mode, actionName, formId, back, Action }) =>
  mode > 1 ? (
    <Div>
      <ActionButton
        text="VOLTAR"
        onClick={back || (() => window.history.back())}
        multi
        grey
      />
      <ActionButton
        text={actionName}
        multi
        type="submit"
        form={formId}
        onClick={Action}
      />
    </Div>
  ) : (
    <ActionButton text="VOLTAR" onClick={() => window.history.back()} grey />
  );

export default ButtonsContainer;
