import styled from "styled-components";

const TextArea = styled.textarea`
  width: calc(100% - 40px);
  border-radius: 5px;
  padding: 15px;
  border: 1px solid ${({ theme }) => theme.border};
  font-size: 20px;
  font-weight: 300;
  line-height: 21px;
  resize: none;
  outline: 0;
  box-shadow: none;
  margin: 0 20px;
  @media (max-width: 768px) {
    margin: 0;
    width: 100%;
  }
`;

export default TextArea;
