import React from "react";
import styled from "styled-components";

const Div = styled.div`
  margin-top: -75px;
  margin-bottom: 50px;
  align-items: center !important;
  @media (max-width: 768px) {
    margin-top: 0;
    flex-direction: column;
    justify-content: flex-start;
    display: flex !important;
    width: 12px;
    margin: 0 auto;
    position: absolute;
    top: 0;
    left: 110px;
  }
`;

const Circle = styled.span`
  display: block;
  background: ${({ theme, done }) => (done ? theme.green : theme.greyAlt)};
  width: 25px;
  height: 25px;
  border-radius: 100%;
  @media (max-width: 768px) {
    height: 12px;
    width: 12px;
  }
`;

const Line = styled.span`
  display: block;
  height: 5px;
  width: 110px;
  background: ${({ theme, done }) => (done ? theme.green : theme.greyAlt)};
  @media (max-width: 768px) {
    height: 52px;
    width: 3px;
  }
`;

const ProgressBar = ({ data }) => (
  <Div>
    {data.map((status, k) => (
      <React.Fragment key={status.label}>
        <Circle done={status.done} />
        {k !== data.length - 1 && <Line done={data[k + 1].done} />}
      </React.Fragment>
    ))}
  </Div>
);

export default ProgressBar;
