import React from "react";
import styled from "styled-components";

const Span = styled.span`
  min-width: 135px;
  max-width: 135px;
  text-align: center;
  font-size: 15px;
  font-weight: 700;
  line-height: 17px;
  color: ${({ theme, done }) => (done ? theme.green : theme.text)};
  * {
    font: inherit;
    color: inherit;
  }
  h4 {
    margin-bottom: 50px;
  }
  h5 {
    b {
      font-size: 12px;
      font-weight: 400;
      line-height: 11px;
    }
  }
  @media (max-width: 768px) {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row-reverse;
    height: 30px;
    width: 100%;
    max-width: initial;
    margin-bottom: 33px;
    h4 {
      margin: 0;
    }
    h5 {
      p {
        font-size: 15px;
        font-weight: 700;
        line-height: 17px;
      }
      b {
        font-size: 10px;
        font-weight: 300;
        line-height: 11px;
      }
    }
  }
`;

const ProgressInfo = ({ label, date, time, done }) => (
  <Span done={done}>
    <h4>{label}</h4>
    {done && (
      <h5>
        <p>{date}</p>
        <b>{time}</b>
      </h5>
    )}
  </Span>
);

export default ProgressInfo;
