import React from "react";
import styled from "styled-components";
import OwnerInfo from "components/common/OwnerInfo";

const Div = styled.div`
  width: 100%;
  text-transform: uppercase;
  h3 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: ${({ theme }) => theme.bgAlt};
    padding: 10px 15px;
    font-size: 25px;
    font-weight: 300;
    margin-bottom: 15px;
    span {
      font: inherit;
    }
    b {
      font: inherit;
      font-weight: 700;
    }
  }
  @media (max-width: 768px) {
    text-align: center;
    h3 {
      margin: 0;
      background: transparent;
      justify-content: center;
      span {
        display: none;
      }
      b {
        font-weight: 400;
      }
    }
  }
`;

const OrderTopInfo = ({ id, client, address }) => (
  <Div>
    <h3>
      <span>PEDIDO</span>
      <b>{id}</b>
    </h3>
    <OwnerInfo client={client} address={address} />
  </Div>
);

export default OrderTopInfo;
