import React from "react";
import styled from "styled-components";
import ActionButton from "components/common/ActionButton";
import arrowIcon from "assets/icons/arrow.png";

const Div = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: -15px;
  h4 {
    width: 100%;
    font-size: 15px;
    font-weight: 300;
    line-height: 16px;
    margin: 20px 0;
  }
  ul {
    width: 100%;
    li {
      cursor: pointer;
      width: fit-content;
      font-size: 20px;
      font-weight: 300;
      margin-bottom: 5px;
      img {
        margin-right: 7px;
      }
      b {
        font: inherit;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
  }
  @media (max-width: 768px) {
    li {
      font-size: 15px;
    }
  }
`;

const OwnerShortcuts = ({ update, data }) => (
  <Div>
    <ActionButton
      onClick={() => update({ newMode: true })}
      text="ADICIONAR NOVO"
      grey
      drown
    />
    <h4>RECENTES</h4>
    <ul>
      {data.slice(0, 3).map(owner => (
        <li key={owner.label} onClick={() => update({ selectedOwner: owner })}>
          <img src={arrowIcon} alt="->" />
          <b>{owner.label}</b>
        </li>
      ))}
    </ul>
  </Div>
);

export default OwnerShortcuts;
