import React, { useContext } from "react";
import styled, { ThemeContext } from "styled-components";
import ReactSelect from "react-select";
import OwnerShortcuts from "./OwnerShortcuts";

const Div = styled.div`
  width: 100%;
  padding: 15px;
  * {
    transition: 0s;
  }
`;

const SelectOwner = ({ update, ...props }) => {
  const { theme } = useContext(ThemeContext);

  const handleChange = selectedOwner => update({ selectedOwner });

  const selectorTheme = t => ({
    ...t,
    colors: {
      ...t.colors,
      text: theme.text,
      primary: theme.grey,
      primary25: theme.shadowLight,
      primary50: theme.accent,
      neutral0: theme.bgAlt,
      neutral20: theme.border,
      neutral30: theme.primary,
      neutral50: theme.text,
      neutral70: theme.white,
      neutral80: theme.text
    }
  });

  const styles = {
    control: provided => ({
      ...provided,
      padding: "10px"
    })
  };

  return (
    <Div>
      <ReactSelect
        value={props.selectedOwner}
        onChange={handleChange}
        options={props.data}
        isSearchable={true}
        placeholder="BUSCAR"
        theme={selectorTheme}
        isClearable={true}
        onMenuOpen={() => update({ optionsOpen: true })}
        onMenuClose={() => update({ optionsOpen: false })}
        styles={styles}
      />
      {!props.optionsOpen && !props.selectedOwner && (
        <OwnerShortcuts update={update} data={props.data} />
      )}
    </Div>
  );
};

export default SelectOwner;
