import React from "react";
import StyledSelect from "../StyledSelect";
import useFetch from "Utils/useFetch";
import { server_url } from "Utils/Connect";

const CitySelect = ({ update, state_id, city }) => {
  const updateSelected = ({ value, id }) =>
    update({
      city: value,
      city_id: id,
      neighborhood: undefined,
      neighborhood_id: undefined
    });

  const [data, loading] = useFetch(
    `${server_url}api/get-city-list`,
    {
      method: "POST",
      headers: new Headers({
        Accept: "application/json, text/plain, */*",
        "Content-Type": "application/json",
        "Access-Control-Allow-Credentials": true
      }),
      body: JSON.stringify({ state_id })
    },
    state_id
  );

  const options = data
    ? data.map(city => ({
        ...city,
        label: city.name,
        value: city.name
      }))
    : [];

  const value = city ? { label: city, value: city } : null;

  return (
    <StyledSelect
      noOptionsMessage={() =>
        loading
          ? "Carregando..."
          : state_id
          ? "nada encontrado"
          : "selecione um Estado"
      }
      options={options}
      loading={loading}
      placeholder="CIDADE"
      updateSelected={updateSelected}
      required
      w={3}
      value={value}
    />
  );
};

export default CitySelect;
