import React from "react";
import styled from "styled-components";

const Title = styled.h1`
  padding: ${({ small }) => (small ? "12px" : "")} 24px;
  display: flex;
  width: 100%;
  align-items: center;
  background-color: ${({ theme }) => theme.bgAlt};
  b {
    color: ${({ theme }) => theme.text};
    font-size: 20px;
    font-weight: 300;
    line-height: 21px;
  }
  img {
    margin-right: 15px;
  }
  @media (max-width: 768px) {
    padding: ${({ small }) => (small ? "8px" : "")} 12px;
    b {
      font-size: 15px;
      font-weight: 300;
      line-height: 16px;
    }
    img {
      margin-right: 12px;
    }
  }
`;

const SectionTitle = ({ icon, title, small }) => (
  <Title small={small}>
    {icon && <img src={icon} alt="icon" />}
    <b>{title}</b>
  </Title>
);

export default SectionTitle;
