import React, { useState } from "react";
import logo from "assets/img/Logo.png";
import AuthWrapper from "components/common/Auth/AuthWrapper";
import LoginForm from "./LoginForm";
import ErrorMsg from "components/common/Auth/ErrorMsg";
import StyledLink from "components/common/Auth/StyledLink";
import ButtonLink from "components/common/Auth/ButtonLink";

const Login = () => {
  const [wrongAuth, setWrongAuth] = useState(false);
  document.title = `SANSTONE | LOGIN`;

  return (
    <AuthWrapper>
      <img src={logo} alt="sanstone" />
      {wrongAuth ? <ErrorMsg msg="Email ou Senha incorretos" /> : null}
      <LoginForm setWrongAuth={setWrongAuth} />
      <StyledLink to="/reset">Esqueci minha senha</StyledLink>
      <ButtonLink to="/register">Cadastrar</ButtonLink>
    </AuthWrapper>
  );
};

export default Login;
