import React from "react";
import { Redirect } from "react-router-dom";
import Container from "components/common/Container";
import { getOrderInfo } from "Utils/Connect";
import { userContext } from "contexts/userContext";
import Hider from "components/Hider";
import OrderTopInfo from "components/common/OrderTopInfo";
import InfoLabels from "./InfoLabels";
import ProgressTracker from "./ProgressTracker";
import ButtonsContainer from "components/common/ButtonsContainer";

class Order extends React.Component {
  state = { loading: true };
  id = this.props.match.params.id;

  static contextType = userContext;

  async componentDidMount() {
    const response = await getOrderInfo(this.id, this.context.access_token);
    if (!response) return this.setState({ notFound: true });
    this.setState({ loading: false, ...response });
  }

  render() {
    if (this.state.notFound)
      return <Redirect to="/notFound?title=Não encontramos o identificador de order" />;
    if (this.state.loading) return <Hider />;
    const { order } = this.state;
    return (
      <Container>
        <OrderTopInfo {...order} />
        <InfoLabels order={order} id={this.id} />
        <ProgressTracker info={order.statusHistory} />
        <ButtonsContainer mode="0" />
      </Container>
    );
  }
}

export default Order;
