import React from "react";
import Title from "components/common/Auth/Title";
import ErrorMsg from "components/common/Auth/ErrorMsg";
import TextInput from "components/common/Auth/TextInput";
import Button from "components/common/Auth/Button";
import withDocMask from "components/common/Forms/withDocMask";

const DocNumberInput = withDocMask(TextInput);

class IdentificationForm extends React.Component {
  state = {
    name: "",
    doc_number: ""
  };

  handleChange = ev => this.setState({ [ev.target.name]: ev.target.value });

  handleSubmit = async ev => {
    ev.preventDefault();
    const { name, doc_number } = this.state;
    this.props.update(ev, { name, doc_number }, true);
  };

  render() {
    return (
      <>
        <Title>Identificação</Title>
        {this.props.error ? <ErrorMsg msg={this.props.error} /> : null}
        <form onSubmit={this.handleSubmit}>
          <TextInput
            type="text"
            placeholder="Nome/Razão Social"
            autoComplete="name"
            required
            value={this.state.name}
            name="name"
            onChange={this.handleChange}
          />
          <DocNumberInput
            placeholder="CPF(123.)/CNPJ(12.)"
            required
            value={this.state.doc_number}
            name="doc_number"
            onChange={this.handleChange}
          />
          <Button type="submit">Cadastrar</Button>
        </form>
      </>
    );
  }
}

export default IdentificationForm;
