import React, { useContext } from "react";
import { ThemeContext } from "styled-components";
import Div from "./menu.styles";
import MenuLink from "./MenuLink";
import UserBox from "./UserBox";
import { userContext } from "contexts/userContext";
import { menusContext } from "contexts/menusContext";

const UserMenu = () => {
  const { name } = useContext(userContext);
  const { dispatch, showUserMenu } = useContext(menusContext);
  const { switchTheme } = useContext(ThemeContext);

  const hideMenu = ev => {
    ev.preventDefault();
    dispatch({ type: "HIDE" });
  };

  const changeTheme = ev => {
    ev.preventDefault();
    switchTheme();
  };

  return (
    <Div id="userMenu" className={showUserMenu ? "open" : ""}>
      <UserBox userName={name.split(" ")[0]} />
      <ul>
        <MenuLink to="/profile" icon="perfil" label="PERFIL" />
        <MenuLink to="/settings" icon="ajustes" label="MODO ESCURO" onClick={changeTheme} />
        <MenuLink to="/help" icon="info" label="AJUDA" />
        <hr />
        <MenuLink to="/logout" icon="sair" label="SAIR" />
      </ul>
      <a href="#user" className="closeBtn" onClick={hideMenu}>
        <span className="icon-fechar" />
      </a>
    </Div>
  );
};

export default UserMenu;
