import styled from "styled-components";

const SectionTitle = styled.h1`
  margin-bottom: 35px;
  color: ${({ theme }) => theme.text};
  font-size: 25px;
  font-weight: 300;
  line-height: 27px;
  text-align: center;

  @media (max-width: 768px) {
    margin-bottom: 15px;
    font-size: 20px;
    font-weight: 500;
  }
  @media (max-width: 640px) {
    font-size: 18px;
  }
`;

export default SectionTitle;
