import React from "react";
import Container from "components/common/Container";
import ccIcon from "assets/icons/credit-card.png";
import ButtonsContainer from "components/common/ButtonsContainer";
import Choices from "components/common/Choices";
import cc2Icon from "assets/icons/icone_credito.png";
import bankIcon from "assets/icons/icone_codigo.png";

class PaymentMethod extends React.Component {
  state = { mode: 0 };
  choices = [
    { name: "CARTÃO DE CRÉDITO", icon: cc2Icon },
    { name: "BOLETO BANCÁRIO", icon: bankIcon }
  ];
  update = obj => {
    if (obj.mode === 1) return this.props.history.push("./credit_card");
    if (obj.mode === 2) return this.props.history.push("./boleto");
    this.setState(obj);
  };
  render() {
    return (
      <Container icon={ccIcon} title="ESCOLHA A FORMA DE PAGAMENTO">
        <Choices
          mode={this.state.mode}
          choices={this.choices}
          update={this.update}
        />
        <ButtonsContainer mode="0" />
      </Container>
    );
  }
}

export default PaymentMethod;
