import React from "react";
import styled from "styled-components";

const Div = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
  @media (max-width: 768px) {
    flex-direction: column;
  }
`;

const Choice = styled.div`
  width: 48%;
  padding: 30px;
  text-align: center;
  cursor: pointer;
  color: ${({ theme, active }) => (active ? theme.primary : theme.text)};
  font-size: 20px;
  font-weight: 300;
  font-weight: ${({ active }) => (active ? "400" : "300")};
  line-height: 21px;
  border-radius: 8px;
  background-color: ${({ theme }) => theme.bgAlt};
  margin-bottom: 15px;
  img {
    margin-right: 8px;
  }
  b {
    font: inherit;
    color: inherit;
  }
  @media (max-width: 768px) {
    width: 100%;
    padding: 15px 0;
    margin-bottom: 12px;
    font-size: 15px;
    line-height: 16px;
  }
`;

const Choices = ({ choices, mode, update }) => (
  <Div>
    {choices.map((choice, i) => (
      <Choice
        choice={choice}
        key={i}
        active={mode === i + 1}
        onClick={() => update({ mode: i + 1 })}
      >
        {choice.icon && <img src={choice.icon} alt="icon" />}
        <b>{choice.name || choice}</b>
      </Choice>
    ))}
  </Div>
);

export default Choices;
