import React from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import formatMoney from "Utils/formatMoney";
import arrowIcon from "assets/icons/arrow.png";

let Div = styled.div`
  text-transform: uppercase;
  border-radius: 5px;
  background-color: ${({ theme }) => theme.bgAlt};
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 25px;
  padding: 15px;
  font-size: 20px;
  font-weight: 300;
  * {
    font: inherit;
  }
  b {
    color: ${({ theme }) => theme.primary};
    font-size: 25px;
    font-weight: 700;
  }
  .icon {
    margin-right: 10px;
  }
  .arrow {
    margin-left: 10px;
  }
  @media (max-width: 768px) {
    padding: 10px;
    font-size: 15px;
    b {
      font-size: 15px;
    }
    .icon {
      margin-right: 5px;
    }
    .arrow {
      margin-left: 5px;
    }
  }
`;

const StyledLink = styled(Link)`
  width: 100%;
`;

const Info = ({ price, icon, title, to, statusText, arrow }) => {
  const content = (
    <Div>
      {icon && (
        <div>
          <img src={icon} alt="icon" className="icon" />
          <i>{title}</i>
        </div>
      )}
      {price !== undefined && <b>{formatMoney(price)}</b>}
      <div>
        <u>{statusText}</u>
        {arrow && <img src={arrowIcon} alt="->" className="arrow" />}
      </div>
    </Div>
  );

  return to ? <StyledLink to={to}>{content}</StyledLink> : content;
};

export default Info;
