import React from "react";
import { Redirect } from "react-router-dom";
import Container from "components/common/Container";
import personIcon from "assets/icons/person.png";
import Choices from "components/common/Choices";
import ButtonsContainer from "components/common/ButtonsContainer";
import SelectOwner from "./SelectOwner";
import { getClients } from "Utils/Connect";
import Hider from "components/Hider";
import NewOwnerForm from "components/common/Forms/NewOwnerForm";
import ErrorMsg from "components/common/Auth/ErrorMsg";

class NewBudgetOwner extends React.Component {
  state = { mode: 0 };
  choices = ["EU MESMO", "MEU CLIENTE"];
  update = obj =>
    this.setState(
      { ...obj, error: null },
      () => obj.mode === 1 && this.submit()
    );
  async componentDidMount() {
    const response = await getClients();
    if (!response) return;
    this.setState({
      data: response.map(({ id, name: value }) => ({ label: value, id, value }))
    });
  }
  submit = ev => {
    if (ev) ev.preventDefault();
    const { state, props } = this;
    let data = { addressInfo: props.location.state, ownerInfo: {} };
    if (state.mode === 1) data.ownerInfo.mySelf = true;
    else if (state.newMode) {
      const { name, doc_number, email, phone } = state;
      data.ownerInfo = { name, doc_number, email, phone };
    } else if (!state.selectedOwner)
      return this.setState({
        error: "Proprietário deve ser selecionado"
      });
    else {
      data.ownerInfo = {
        owner: state.selectedOwner.value,
        client_id: state.selectedOwner.id
      };
    }
    this.props.history.push("/new_budget/type", data);
  };

  render() {
    const { state, props, update, submit } = this;
    if (!props.location.state) return <Redirect to="/new_budget" />;
    if (!state.data) return <Hider />;
    return (
      <Container icon={personIcon} title="TITULAR DA COMPRA">
        <Choices choices={this.choices} update={update} mode={state.mode} />

        <ErrorMsg msg={state.error} />
        {state.mode === 2 && (
          <form id="budgetOwner" onSubmit={submit} style={{ width: "100%" }}>
            {state.newMode ? (
              <NewOwnerForm update={update} />
            ) : (
              <SelectOwner {...state} update={update} submit={submit} />
            )}
          </form>
        )}

        <ButtonsContainer
          mode={state.mode}
          actionName="AVANÇAR"
          formId="budgetOwner"
          back={state.newMode && (() => this.setState({ newMode: false }))}
        />
      </Container>
    );
  }
}

export default NewBudgetOwner;
