import React from "react";
import styled from "styled-components";
import Container from "components/common/Container";
import agendaIcon from "assets/icons/agenda.png";
import { getVisits } from "Utils/Connect";
import Hider from "components/Hider";
import VisitInfo from "./VisitInfo";
import ActionButton from "components/common/ActionButton";
import DeleteVisit from "./DeleteVisit";

const Div = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  @media (max-width: 768px) {
    flex-direction: column;
    align-items: center;
  }
`;

class VisitPreview extends React.Component {
  state = {
    data: null,
    loading: true
  };
  async componentDidMount() {
    const visits = await getVisits();
    const targetId = this.props.match.params.id;
    const data = visits.find(({ id }) => id === targetId);
    if (data) return this.setState({ data, loading: false });
    this.props.history.push("/VisitNotFound?title=VISITA NÃO ENCONTRADA");
  }

  render() {
    if (this.state.loading) return <Hider />;
    return (
      <Container icon={agendaIcon} title="VISITAS AGENDADAS">
        <Div>
          <VisitInfo data={this.state.data} />
          <DeleteVisit id={this.state.data.id} />
        </Div>
        <ActionButton text="VOLTAR" to="/visits" grey />
      </Container>
    );
  }
}

export default VisitPreview;
