import styled from "styled-components";

export default styled.div`
  position: fixed;
  top: 0;
  right: 0;
  width: 0;
  height: 0;
  z-index: 15;
  background-color: ${({ theme }) => theme.bgAlt};
  box-shadow: 2px 2px 10px 0 ${({ theme }) => theme.shadow};
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  overflow: hidden;
  > * {
    display: none;
  }
  &.open {
    height: 470px;
    width: 230px;
  }
  &.open ul {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    hr {
      margin-top: 34px;
      margin-bottom: 10px;
      border: 0;
      width: 147px;
      border-top: solid 1px ${({ theme }) => theme.border};
    }
  }
  &.open .closeBtn {
    display: block;
    span {
      font-size: 25px;
      color: ${({ theme }) => theme.text};
    }
  }
`;
