import React from "react";
import Container from "components/common/Container";
import SectionTitle from "components/common/SectionTitle";
import personIcon from "assets/icons/person.png";
import mapIcon from "assets/icons/map.png";
import ActionButton from "components/common/ActionButton";
import PersonalForm from "components/common/Forms/PersonalForm";
import AddressForm from "components/common/AddressForm/AddressForm";
import GenderSelect from "components/common/Forms/GenderSelect";
import SecurityForm from "components/common/Forms/SecurityForm";
import Form from "components/common/Forms/Form";
import { getClients } from "Utils/Connect";
import Hider from "components/Hider";

class EditClient extends React.Component {
  state = { loading: true };
  update = obj => this.setState(obj);
  submit = ev => {
    ev.preventDefault();
    console.log("Client Edited");
  };
  async componentDidMount() {
    const clients = await getClients();
    const id = this.props.match.params.id;
    const target = clients.find(client => client.id === id);
    for (let key in target) if (!target[key]) target[key] = "";
    if (target) {
      return this.setState({ ...target, loading: false });
    }
    this.props.history.push("/soon?title=cliente não encontrado");
  }

  render() {
    if (this.state.loading) return <Hider />;
    return (
      <Container>
        <Form onSubmit={this.submit}>
          <SectionTitle icon={personIcon} title="DADOS PESSOAIS" small />
          <PersonalForm update={this.update} info={this.state} />
          <GenderSelect
            update={this.update}
            info={this.state}
            required
            margin="-15px 0 0 0"
          />
          <SecurityForm update={this.update} info={this.state} emailOnly />

          <SectionTitle icon={mapIcon} title="ENDEREÇO" small />
          <AddressForm update={this.update} info={this.state} />

          <ActionButton type="submit" text="SALVAR" />
        </Form>
      </Container>
    );
  }
}

export default EditClient;
