import React from "react";
import styled from "styled-components";
import FilterItem from "./FilterItem";

const Li = styled(FilterItem)`
  span.icon-fechar {
    display: none;
  }
  &.open span.icon-seta {
    transform: rotate(180deg);
  }
  span.icon-seta:before {
    font-size: 20px;
    font-weight: 300;
    line-height: 21px;
  }
  @media (max-width: 768px) {
    height: 30px;
    border-radius: 5px;
    border: 1px solid ${({ theme }) => theme.border};
    justify-content: center;
    span.icon-seta {
      display: none;
    }
    &.open {
      border: 0;
      width: auto;
      padding: 0;
      margin-left: 15px;
      b {
        display: none;
      }
      span.icon-fechar {
        display: block;
      }
    }
  }
`;

const ToggleFilter = props => (
  <Li {...props}>
    <b>FILTRAR</b>
    <span className="icon-seta" />
    <span className="icon-fechar" />
  </Li>
);

export default ToggleFilter;
