import React from "react";
import styled from "styled-components";
import formatMoney from "Utils/formatMoney";

const Div = styled.div`
  width: 40%;
  border-radius: 5px;
  background-color: ${({ theme }) => theme.primary};
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: ${({ cashed }) => (cashed ? "space-between" : "center")};
  margin-right: 20px;
  div {
    color: ${({ theme }) => theme.white};
    h5 {
      font-size: 20px;
      font-weight: 300;
      line-height: 21px;
      color: inherit;
    }
    h6 {
      font-size: 30px;
      font-weight: 700;
      line-height: 33px;
      color: inherit;
    }
  }
  .cashback {
    color: ${({ theme }) => theme.grey};
    h5 {
      font-size: 15px;
      line-height: 16px;
    }
    h6 {
      font-size: 25px;
      line-height: 28px;
    }
  }
  @media (max-width: 768px) {
    width: 100%;
    padding: 15px 10px;
    margin-bottom: 20px;
    justify-content: ${({ cashed }) => (cashed ? "space-between" : "flex-end")};
    flex-direction: row;
    align-items: center;
    div,
    .cashback {
      h5 {
        font-size: 10px;
        line-height: 11px;
      }
      h6 {
        font-size: 15px;
        line-height: 17px;
      }
    }
  }
`;

const Total = ({ cashback = 0, price, freight, cashed }) => (
  <Div cashed={cashed}>
    {cashed && (
      <div className="cashback">
        <h5>DESCONTO</h5>
        <h6>{formatMoney(cashback)}</h6>
      </div>
    )}
    <div>
      <h5>TOTAL A PAGAR</h5>
      <h6>{formatMoney(price + freight - (cashed ? cashback : 0))}</h6>
    </div>
  </Div>
);

export default Total;
