import React from "react";
import AuthWrapper from "components/common/Auth/AuthWrapper";
import StyledLink from "components/common/Auth/StyledLink";
import ButtonLink from "components/common/Auth/ButtonLink";
import Step from "./Step";
import ProgressTrack from "./ProgressTrack";
import { SignUp } from "Utils/Connect";

class Register extends React.Component {
  state = {
    mode: 0,
    type_id: "",
    gender: "",
    email: "",
    password: "",
    name: "",
    doc_number: "",
    error: ""
  };

  updateVal = async (ev, obj, noPrevent) => {
    if (!noPrevent) ev.preventDefault();
    const { mode } = this.state;
    if (mode < 3) return this.setState({ ...obj, mode: mode + 1 });
    else await this.setState(obj);
    const response = await SignUp(this.state);
    if (!response || !response.access_token)
      return this.setState({ error: "Erro de Registo" });
    else return this.setState({ error: "", mode: 4 });
  };

  back = ev => {
    ev.preventDefault();
    const { mode } = this.state;
    if (mode > 0) this.setState({ mode: mode - 1 });
    else window.history.back();
  };

  render() {
    const StepForm = Step(this.state.mode);
    document.title = `SANSTONE | CADASTRAR`;
    return (
      <AuthWrapper>
        <StepForm update={this.updateVal} error={this.state.error} />
        {this.state.mode < 4 ? (
          <>
            <StyledLink to="#back" onClick={this.back}>
              Voltar
            </StyledLink>
            <ButtonLink to="/login">login</ButtonLink>
            <ProgressTrack progress={this.state.mode} />
          </>
        ) : null}
      </AuthWrapper>
    );
  }
}

export default Register;
