import React from "react";
import styled from "styled-components";
import formatMoney from "Utils/formatMoney";

const Div = styled.div`
  border-radius: 5px;
  background-color: ${({ theme }) => theme.bgAlt};
  padding: 0 30px;
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 20px 0;
  .details {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15px 0;
    border-bottom: solid 1px ${({ theme }) => theme.border};
    span {
      flex-grow: 1;
      display: flex;
      flex-direction: column;
      align-items: center;
      u {
        font-size: 15px;
        font-weight: 300;
        line-height: 16px;
        margin-bottom: 5px;
      }
      b {
        font-size: 20px;
        font-weight: 700;
        line-height: 22px;
      }
    }
  }
  .total {
    display: flex;
    flex-direction: column;
    padding: 15px 5px;
    align-items: center;
    u {
      font-size: 15px;
      font-weight: 300;
      line-height: 16px;
    }
    b {
      font-size: 25px;
      font-weight: 700;
      line-height: 28px;
    }
    span {
      display: none;
      font-size: 22px;
      font-weight: 700;
    }
  }

  @media (max-width: 768px) {
    padding: 0 10px;
    flex-direction: column-reverse;
    .details {
      display: ${({ active }) => (active ? "flex" : "none")};
      padding: 20px 0;
      border-bottom: 0;
      border-top: solid 1px ${({ theme }) => theme.border};
      span {
        u {
          font-size: 8px;
          line-height: 9px;
        }
        b {
          font-size: 10px;
          line-height: 11px;
        }
      }
    }
    .total {
      justify-content: space-between;
      flex-direction: row;
      u {
        display: none;
      }
      span {
        display: block;
        transform: rotate(${({ active }) => (active ? 180 : 0)}deg);
      }
      b {
        font-size: 15px;
        font-weight: 700;
        line-height: 17px;
      }
    }
  }
`;

const PriceInfo = ({ price, freight, others }) => {
  let [active, setActive] = React.useState(false);
  const toggleDetails = () => setActive(!active);

  return (
    <Div active={active}>
      <div className="details">
        <span>
          <u>TOTAL MATERIAL</u>
          <b>{formatMoney(price + freight)}</b>
        </span>
        <span>
          <u>TOTAL PRODUTO</u>
          <b>{formatMoney(price)}</b>
        </span>
        <span>
          <u>FRETE</u>
          <b>{formatMoney(freight)}</b>
        </span>
        <span>
          <u>OUTRAS DESPESAS</u>
          <b>{formatMoney(others)}</b>
        </span>
      </div>
      <div className="total" onClick={toggleDetails}>
        <u>TOTAL GERAL</u>
        <b>{formatMoney(price + freight)}</b>
        <span className="icon-seta" />
      </div>
    </Div>
  );
};

export default PriceInfo;
