import styled from "styled-components";
import React from "react";

const Label = styled.label`
  height: 50px;
  width: 63%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 20px;
  font-weight: 300;
  margin-bottom: 16px;
  b {
    font: inherit;
  }
  @media (max-width: 768px) {
    height: 40px;
    font-size: 17px;
  }
`;

const TextLabel = ({ text }) => (
  <Label>
    <b>{text}</b>
  </Label>
);

export default TextLabel;
