import React from "react";
import styled from "styled-components";

const Item = styled.li`
  margin-bottom: 25px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  img {
    margin-right: 15px;
  }
  b {
    font-size: 20px;
    font-weight: 300;
    line-height: 21px;
  }

  @media (max-width: 768px) {
    b {
      font-size: 15px;
    }
  }
`;

const ListItem = ({ icon, text }) => (
  <Item>
    <img src={icon} alt="icon" />
    <b>{text}</b>
  </Item>
);

export default ListItem;
