import React from "react";
import Item from "./Item";
import formatMoney from "Utils/formatMoney";
import styled from "styled-components";

const Div = styled.div`
  width: 100%;
`;

const ItemList = ({ data, dataType }) => {
  const props = item =>
    ({
      budget: {
        to: `/budgets/${item.id}`,
        title: item.id + (item.client ? `- ${item.client}` : ""),
        subTitle: item.address,
        color: item.statusColor
      },
      order: {
        to: `/orders/${item.id}`,
        title: item.id + (item.client ? `- ${item.client}` : ""),
        subTitle: item.address,
        color: item.statusColor
      },
      visit: {
        to: `/visits/${item.id}`,
        title: `${item.day} - ${item.date} - ${item.time}`,
        subTitle: item.address,
        color: item.color
      },
      receipt: {
        to: `/receipts/${item.id}`,
        title: item.id + (item.date ? ` - ${item.date}` : ""),
        subTitle: formatMoney(item.price),
        color: item.color
      }
    }[dataType] || {});

  return (
    <Div>
      {data.map(item => (
        <Item key={item.id} {...props(item)} />
      ))}
    </Div>
  );
};

export default ItemList;
