import React from "react";
import FormInput from "../Forms/FormInput";
import FormContainer from "../Forms/FormContainer";
import TextLabel from "../Forms/TextLabel";
import { address, toObj } from "../Forms/config";
import StateSelect from "./StateSelect";
import CitySelect from "./CitySelect";
import NeighborhoodSelect from "./NeighborhoodSelect";

class AddressForm extends React.Component {
  state = {
    ...toObj(address),
    ...this.props.info
  };
  handleChange = ev => {
    this.setState({ [ev.target.name]: ev.target.value });
    this.props.update({ [ev.target.name]: ev.target.value });
  };

  selectUpdate = obj => {
    this.props.update(obj);
    this.setState(obj);
  };

  render() {
    const { required } = this.props;
    return (
      <FormContainer>
        <FormInput
          value={this.state.CEP || this.state[address[0]]}
          type="text"
          name={this.state.CEP ? "CEP" : address[0]}
          label="CEP"
          onChange={this.handleChange}
          pattern="\d{5}-\d{3}"
          mask="99999-999"
          w="1"
          required={required}
        />
        <TextLabel text="NÃO SEI O CEP" />
        <FormInput
          value={this.state[address[1]]}
          type="text"
          name={address[1]}
          label="ENDEREÇO"
          onChange={this.handleChange}
          w="4"
          required={required}
        />
        <FormInput
          value={this.state[address[2]]}
          type="text"
          name={address[2]}
          label="NÚMERO"
          onChange={this.handleChange}
          w="1"
          required={required}
        />
        <FormInput
          value={this.state[address[3]]}
          type="text"
          name={address[3]}
          label="COMPLEMENTO"
          onChange={this.handleChange}
          w="3"
        />
        <StateSelect update={this.selectUpdate} {...this.state} />
        <CitySelect update={this.selectUpdate} {...this.state} />
        <NeighborhoodSelect update={this.selectUpdate} {...this.state} />
      </FormContainer>
    );
  }
}

export default AddressForm;
