import React from "react";
import styled from "styled-components";
import ListTitle from "./ListTitle";
import ListItem from "./ListItem";

const Div = styled.div`
  width: 100%;
  margin-top: 25px;
`;

const List = ({ name, data, actions }) => {
  let [mode, setMode] = React.useState(0);

  return (
    <Div>
      <ListTitle name={name} mode={mode} actions={actions} setMode={setMode} />
      {data.map(p => (
        <ListItem key={p.id} {...p} editMode={mode === 2} actions={actions} />
      ))}
    </Div>
  );
};

export default List;
