import React from "react";
import styled from "styled-components";
import Container from "components/common/Container";
import linkIcon from "assets/icons/link-2.png";
import FormInput from "components/common/Forms/FormInput";
import ButtonsContainer from "components/common/ButtonsContainer";
import FormContainer from "components/common/Forms/FormContainer";
import Done from "components/common/Done";

const H2 = styled.h2`
  font-size: 15px;
  font-weight: 300;
  @media (max-width: 768px) {
    font-size: 12px;
  }
`;

class PaymentSendLink extends React.Component {
  state = { email: "" };
  id = this.props.match.params.id;
  handleChange = ({ target }) => this.setState({ email: target.value });
  submit = ev => {
    ev.preventDefault();
    this.setState({ done: true });
  };
  render() {
    if (this.state.done)
      return (
        <Done
          title="LINK ENVIADO"
          text="VOCÊ SERÁ NOTIFICADO QUANDO O PAGAMENTO FOR EFETUADO."
          to="./"
        />
      );
    return (
      <Container icon={linkIcon} title="ENVIAR LINK DE PAGAMENTO">
        <H2>INFORME O EMAIL QUE DESEJA ENVIAR</H2>
        <form id="paySendLink" onSubmit={this.submit} style={{ width: "100%" }}>
          <FormContainer>
            <FormInput
              value={this.state.email}
              type="email"
              name="email"
              label="EMAIL"
              onChange={this.handleChange}
              w="4"
              autoComplete="email"
              required
            />
          </FormContainer>
        </form>
        <ButtonsContainer mode="2" actionName="ENVIAR" formId="paySendLink" />
      </Container>
    );
  }
}

export default PaymentSendLink;
