import React from "react";
import Container from "components/common/Container";
import agendaIcon from "assets/icons/agenda.png";
import Filter from "components/common/Filter/index";
import ItemList from "components/common/ItemList";
import { getVisits } from "Utils/Connect";
import ActionButton from "components/common/ActionButton";
import Hider from "components/Hider";

class VisitsList extends React.Component {
  state = {
    data: null,
    filter: null
  };

  async componentDidMount() {
    const data = await getVisits();
    this.setState({ data });
  }

  filterData = () =>
    this.state.data.filter(({ completed }) => completed !== this.state.filter);

  render() {
    return this.state.data ? (
      <Container icon={agendaIcon} title="VISITAS AGENDADAS">
        <Filter setFilter={filter => this.setState({ filter })} />
        {this.state.data && (
          <ItemList data={this.filterData()} dataType="visit" />
        )}
        <ActionButton to="/visits/new" text="NOVO AGENDAMENTO" />
      </Container>
    ) : (
      <Hider />
    );
  }
}

export default VisitsList;
