import styled from "styled-components";
import CardStyled from "./card.styles";

export default styled(CardStyled)`
  dl {
    margin-top: 24px;
    dt,
    dd {
      font-size: 15px;
      font-weight: 300;
      line-height: 17px;
    }

    dd {
      font-weight: 700;
      margin-bottom: 24px;
    }
  }
  @media (max-width: 860px) {
    dl {
      margin: 0;
      dt,
      dd {
        font-size: 14px;
      }
    }
  }
  @media (max-width: 640px) {
    dl {
      dt:last-of-type,
      dd:last-of-type {
        display: none;
      }
      dd:first-of-type {
        font-size: 15px;
        font-weight: 700;
        line-height: 17px;
      }
      dd {
        font-size: 10px;
        margin-bottom: 10px;
      }
    }
  }
`;
