import React, { useReducer, createContext } from "react";

const initialState = {
  showUserMenu: false,
  showNavMenu: false,
  showNotificationMenu: false
};
const menusContext = createContext(initialState);

const storeReducer = (state, action) => {
  let menu = action.menu ? `show${action.menu}Menu` : null;
  switch (action.type) {
    case "HIDE":
      return initialState;
    case "SHOW":
      return {
        ...initialState,
        [menu]: true
      };
    case "TOGGLE":
      return {
        ...initialState,
        [menu]: !state[menu]
      };
    default:
      return state;
  }
};

const MenusContextProvider = ({ children }) => {
  const [state, dispatch] = useReducer(storeReducer, initialState);
  return <menusContext.Provider value={{ ...state, dispatch }}>{children}</menusContext.Provider>;
};

export { menusContext, MenusContextProvider };
