import { useEffect } from "react";

const RouteUpdates = ({ location: { pathname }, routeTitle }) => {
  useEffect(() => {
    document.title = routeTitle ? `SANSTONE | ${routeTitle}` : "SANSTONE";
    window.scrollTo({
      top: 0,
      left: 0,
      behavior: "smooth"
    });
  }, [pathname, routeTitle]);

  return null;
};

export default RouteUpdates;
