import React from "react";
import styled from "styled-components";
import AvatarPreview from "./AvatarPreview";
import uploadIcon from "assets/icons/cloud-upload.png";

const Label = styled.label`
  padding: 5px 15px;
  border-radius: 5px;
  background-color: ${({ theme }) => theme.bgAlt};
  display: block;
  border: 0;
  img {
    filter: brightness(0.5);
    margin-right: 5px;
  }
  b {
    font-size: 12px;
    font-weight: 300;
    line-height: 13px;
  }
  input {
    display: none;
  }
  @media (max-width: 768px) {
    height: auto;
    width: auto;
    padding: 0;
    background: transparent;
    img {
      display: none;
    }
    b {
      font-size: 8px;
      font-weight: 300;
      line-height: 9px;
    }
  }
`;

const AvatarLabel = ({ preview, handleChange }) => (
  <Label>
    <AvatarPreview preview={preview} mobile />
    <img src={uploadIcon} alt="upload" />
    <b>ALTERAR FOTO</b>
    <input type="file" accept="image/*" onChange={handleChange} />
  </Label>
);

export default AvatarLabel;
