import React from "react";
import styled from "styled-components";
import formatMoney from "Utils/formatMoney";

const Div = styled.div`
  margin-top: 200px;
  width: 100%;
  h2 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 120px;
    background-color: ${({ theme }) => theme.bgAlt};
    padding: 20px 15px;
    h3 {
      color: ${({ theme }) => theme.text};
      font-size: 25px;
      font-weight: 700;
      line-height: 28px;
    }
    h4 {
      color: ${({ theme }) => theme.primary};
      font-size: 35px;
      font-weight: 700;
      line-height: 39px;
    }
    .icon-seta {
      font-size: 30px;
      transform: rotate(${({ open }) => (open ? 180 : 0)}deg);
    }
  }
  .details {
    margin-top: 10px;
    h5,
    h6 {
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      span {
        text-align: center;
        display: block;
        width: 33%;
        margin-bottom: 5px;
        font-size: 15px;
        font-weight: 700;
        line-height: 17px;
      }
    }
    h6 span {
      font-size: 20px;
      font-weight: 300;
      line-height: 21px;
    }
  }
  @media (max-width: 768px) {
    margin-top: 55px;
    h2 {
      padding: 10px;
      height: 80px;
      h3 {
        font-size: 20px;
        line-height: 22px;
      }
      h4 {
        font-size: 25px;
        line-height: 28px;
      }
    }
    .details {
      margin-top: 10px;

      h5 span {
        font-size: 8px;
        line-height: 9px;
      }
      h6 span {
        font-size: 10px;
        line-height: 11px;
      }
    }
  }
`;

const SumInfo = ({ order }) => {
  let [open, setOpen] = React.useState(false);
  const toggle = () => setOpen(!open);

  return (
    <Div open={open}>
      <h2 onClick={toggle}>
        <div>
          <h3>TOTAL GERAL</h3>
          <h4>{formatMoney(order.price + order.freight)}</h4>
        </div>
        <span className="icon-seta" />
      </h2>
      {open && (
        <div className="details">
          <h5>
            <span>TOTAL PRODUTO</span>
            <span>FRETE</span>
            <span>OUTRAS DESPESAS</span>
          </h5>
          <h6>
            <span>{formatMoney(order.price)}</span>
            <span>{formatMoney(order.freight)}</span>
            <span>{formatMoney(order.others)}</span>
          </h6>
        </div>
      )}
    </Div>
  );
};

export default SumInfo;
