import React from "react";
import styled from "styled-components";
import infoIcon from "assets/icons/info.png";

const Div = styled.div`
  position: relative;
  padding: 5px;
  b {
    display: none;
    position: absolute;
    font-size: 15px;
    font-weight: 300;
    line-height: 16px;
    border-radius: 8px;
    background-color: rgba(0, 0, 0, 0.1);
    width: auto;
    height: auto;
    bottom: 0;
    padding: 20px 5px;
    left: 0;
    height: 100.31px;
    width: 171.97px;
    z-index: 8;
  }
  &:hover b {
    display: flex;
  }
  @media (max-width: 768px) {
    margin-top: 15px;
    b {
      width: 200px;
      padding: 0 0 0 35px;
      height: 50px;
      align-items: center;
      bottom: -10px;
    }
  }
`;

const InfoTooltip = ({ msg }) => (
  <Div>
    <img src={infoIcon} alt="i" />
    <b>{msg}</b>
  </Div>
);

export default InfoTooltip;
