import styled from "styled-components";

const ItemSubtitle = styled.h4`
  font-size: 20px;
  font-weight: 300;
  line-height: 21px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  @media (max-width: 768px) {
    font-size: 15px;
    font-weight: 300;
    line-height: 16px;
  }
`;

export default ItemSubtitle;
