import React from "react";
import styled from "styled-components";

const H3 = styled.h3`
  text-align: left;
  padding: 0 15px;
  border-bottom: ${({ theme }) => theme.shadowLight} solid 1px;
  color: ${({ theme }) => theme.text};
  height: 50px;
  font-size: 15px;
  font-weight: 400;
  display: flex;
  justify-content: space-between;
  align-items: center;
  b {
    font: inherit;
    color: inherit;
  }
  span {
    color: ${({ theme }) => theme.white};
    font: inherit;
    display: block;
    width: 30px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    border-radius: 100%;
    background: ${({ theme }) => theme.gradient(56)};
  }
`;

const NotificationsHeader = ({ notificationsCount }) => (
  <H3>
    <b>NOTIFICAÇÕES</b>
    <span>{notificationsCount}</span>
  </H3>
);

export default NotificationsHeader;
