import React from "react";
import { Redirect } from "react-router-dom";
import Hider from "components/Hider";
import { GetToken, GetUser } from "Utils/Connect";
import { userContext } from "contexts/userContext";
import validateAddress from "Utils/validateAddress";

class AuthTester extends React.Component {
  state = {
    loading: true,
    logged: false
  };

  static contextType = userContext;

  async componentDidMount() {
    if (this.context.name) return this.setState({ logged: true, loading: false });

    const response = await GetToken();
    if (response === null || !response.logged)
      return this.setState({ logged: false, loading: false });

    const userInfo = await GetUser(response.access_token);
    let hasValidAddress = await validateAddress(userInfo);
    hasValidAddress = hasValidAddress && typeof hasValidAddress !== "string";
    await this.context.update({
      access_token: response.access_token,
      ...userInfo,
      hasValidAddress
    });

    return this.setState({ loading: false, logged: true });
  }

  render() {
    if (this.state.loading) {
      return <Hider />;
    }
    return this.state.logged ? this.props.children : <Redirect to="/login" />;
  }
}

export default AuthTester;
