import React from "react";
import styled from "styled-components";

const Div = styled.div`
  box-shadow: 0 2px 2px 0 ${({ theme }) => theme.shadowLight};
  border-radius: 5px;
  background: ${({ theme }) => theme.bg};
  padding: 20px;
  min-width: calc(50% - 10px);
  max-width: calc(50% - 10px);
  margin-right: 20px;
  &:last-child {
    margin: 0;
  }
  .videoContainer {
    position: relative;
    padding-bottom: 56.25%;
    padding-top: 25px;
    height: 0;
    iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  h5 {
    font-size: 20px;
    font-weight: 300;
    line-height: 21px;
    margin-top: 25px;
  }
  @media (max-width: 768px) {
    padding: 15px;
    min-width: calc(75% - 10px);
    max-width: calc(75% - 10px);
    h5 {
      margin-top: 15px;
      font-size: 15px;
      line-height: 16px;
    }
  }
`;

const VideoCard = ({ src, label }) => (
  <Div>
    <div className="videoContainer">
      <iframe
        title="video1"
        width="410"
        height="230.625"
        src={`https://www.youtube.com/embed/${src}?rel=0&color=white`}
        frameBorder="0"
        allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
        allowFullScreen
      />{" "}
    </div>

    <h5>{label}</h5>
  </Div>
);

export default VideoCard;
