import React from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";

const Div = styled.div`
  overflow-y: scroll;
  height: calc(100% - 50px);
  a {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-bottom: ${({ theme }) => theme.shadowLight} solid 1px;
    padding: 20px 15px;
    background: ${({ theme }) => theme.bgAlt};
    img {
      margin-right: 15px;
    }
    h4 {
      font-weight: 400;
      font-size: 15px;
      color: ${({ theme }) => theme.text};
    }
    b {
      font-weight: 300;
      font-size: 14px;
      color: ${({ theme }) => theme.grey};
    }
  }
  a:hover {
    background: ${({ theme }) => theme.shadowLight};
  }
`;

const NotificationsBody = ({ notifications }) => (
  <Div className="customScrollbars">
    {notifications.map((n, i) => (
      <Link key={i} to={n.link}>
        <img src={n.img} alt=" " />
        <span>
          <h4>{n.text}</h4>
          <b>{n.time}</b>
        </span>
      </Link>
    ))}
  </Div>
);

export default NotificationsBody;
