import React from "react";
import styled from "styled-components";
import SocialLinks from "./SocialLinks";

const FooterWrapper = styled.footer`
  background: ${({ theme }) => theme.gradient(58)};
  box-shadow: 0px 10px 10px 10px ${({ theme }) => theme.shadowLight};
  > div {
    margin: 0 auto;
    padding-top: 20px;
    padding-bottom: 50px;
    width: calc(100% - 30px);
    max-width: 1092px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  h2,
  b {
    font-size: 16px;
    font-weight: 300;
    color: ${({ theme }) => theme.white};
  }
  h2 {
    font-size: 20px;
  }
  @media (max-width: 768px) {
    > div {
      flex-direction: column;
      padding-bottom: 85px;
    }
  }
`;

const Footer = () => (
  <FooterWrapper>
    <div>
      <a href="mailto:contato@sanstone.com.br">
        <b>contato@sanstone.com.br</b>
      </a>
      <SocialLinks />
      <h2>©Sanstone 2019</h2>
    </div>
  </FooterWrapper>
);

export default Footer;
