import React from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";

let Button = styled.div`
  display: block;
  width: 48%;
  border-radius: 8px;
  background: ${({ theme, grey }) =>
    grey ? theme.gradientGrey(0) : theme.gradient(58)};
  box-shadow: 0 2px 10px ${({ theme }) => theme.shadow};
  font-size: 20px;
  font-weight: 300;
  line-height: 21px;
  text-align: center;
  padding: 15px 0;
  margin-top: 25px;
  cursor: pointer;
  transition: none;
  border: 0;
  user-select: none;
  color: ${({ theme }) => theme.white};
  b {
    font: inherit;
    color: inherit;
  }
  img {
    margin-right: 5px;
  }
  &:hover {
    background: ${({ theme }) => theme.accent};
    background: ${({ grey, theme }) => (grey ? theme.grey : theme.accent)};
  }
  input {
    display: none;
  }
  @media (max-width: 768px) {
    position: ${({ drown }) => (drown ? "drown" : "fixed")};
    bottom: 25px;
    margin: 0;
    margin-top: ${({ drown }) => drown && "15px"};
    z-index: 8;
    width: ${({ grey, multi, drown }) => {
      if (drown) return "80%";
      return multi
        ? grey
          ? "48px"
          : "calc(100% - 112px)"
        : "calc(100% - 50px)";
    }};
    left: ${({ grey, multi }) => (!grey && multi ? "87px" : "25px")};
    ${({ grey, multi }) =>
      grey && multi && `b {display: none;}&::before {content: "<";}`}
  }
`;

const ActionButton = ({
  text,
  grey,
  multi,
  drown,
  icon,
  children,
  ...props
}) => {
  if (props.type === "submit") Button = Button.withComponent("button");
  else if (props.to) Button = Button.withComponent(Link);
  else if (icon) Button = Button.withComponent("label");
  else Button = Button.withComponent("div");

  return (
    <Button
      {...props}
      grey={grey ? 1 : 0}
      multi={multi ? 1 : 0}
      drown={drown ? 1 : 0}
    >
      {icon && <img src={icon} alt="icon" />}
      <b>{text}</b>
      {children}
    </Button>
  );
};

export default ActionButton;
