import React from "react";
import Container from "components/common/Container";
import walletIcon from "assets/icons/wallet.png";
import Filter from "components/common/Filter/index";
import ItemList from "components/common/ItemList";
import { getReceipts } from "Utils/Connect";
import Hider from "components/Hider";

class ReceiptsList extends React.Component {
  state = {
    data: null,
    filter: null
  };

  async componentDidMount() {
    const data = await getReceipts();
    this.setState({ data });
  }

  filterData = () =>
    this.state.data.filter(({ completed }) => completed !== this.state.filter);

  render() {
    return this.state.data ? (
      <Container icon={walletIcon} title="SEUS RECEBIMENTOS">
        <Filter setFilter={filter => this.setState({ filter })} />
        {this.state.data && (
          <ItemList data={this.filterData()} dataType="receipt" />
        )}
      </Container>
    ) : (
      <Hider />
    );
  }
}

export default ReceiptsList;
