import React from "react";
import styled from "styled-components";
import { Redirect } from "react-router-dom";
import { userContext } from "contexts/userContext";
import flagIcon from "assets/icons/flag.png";
import ButtonsContainer from "components/common/ButtonsContainer";
import FormInput from "components/common/Forms/FormInput";
import ErrorMsg from "components/common/Auth/ErrorMsg";
import Container from "components/common/Container";
import { CreateOrder } from "Utils/Connect";
import FormContainer from "components/common/Forms/FormContainer";

const H2 = styled.h2`
  font-size: 20px;
  font-weight: 300;
  text-align: center;
  margin-bottom: 35px;
  @media (max-width: 768px) {
    margin: 35px 10px;
    font-size: 15px;
  }
`;

class NewBudgetName extends React.Component {
  state = { name: "" };

  static contextType = userContext;

  submit = async ev => {
    ev.preventDefault();
    const { addressInfo, ownerInfo, typeInfo } = this.props.location.state;
    let data = {
      ...addressInfo,
      ...ownerInfo,
      ...typeInfo,
      name: this.state.name
    };
    const response = await CreateOrder(this.context.access_token, data);
    if (!response.order) return this.setState({ error: "Não é possível criar orçamento" });
    this.props.history.push(`/budgets/${response.order}`);
  };

  handleChange = ev => this.setState({ [ev.target.name]: ev.target.value });

  render() {
    const { state, submit } = this;
    if (!this.props.location.state) return <Redirect to="/new_budget" />;
    return (
      <Container icon={flagIcon} title="ENDEREÇO DE ENTREGA">
        <ErrorMsg msg={state.error} />
        <form onSubmit={submit} id="budgetName" style={{ width: "100%" }}>
          <H2>IDENTIFIQUE RAPIDAMENTE O ORÇAMENTO NOMEANDO A OBRA</H2>
          <FormContainer>
            <FormInput
              name="name"
              label="NOME DA OBRA"
              value={state.name}
              onChange={this.handleChange}
              required
              w="4"
            />
          </FormContainer>
        </form>
        <ButtonsContainer mode="2" actionName="AVANÇAR" formId="budgetName" />
      </Container>
    );
  }
}

export default NewBudgetName;
