import React from "react";
import FormContainer from "./FormContainer";
import RadioLabel from "./RadioLabel";

class GenderSelect extends React.Component {
  state = {
    gender: "M",
    ...this.props.info
  };
  handleChange = ev => {
    this.setState({ gender: ev.target.value });
    this.props.update({ gender: ev.target.value });
  };
  render() {
    const { required, margin } = this.props;
    const { gender } = this.state;
    return (
      <FormContainer margin={margin}>
        <RadioLabel checked={gender === "M"}>
          <input
            type="radio"
            name="gender"
            value="M"
            onChange={this.handleChange}
            checked={gender === "M"}
            required={required}
          />
          <b>HOMEM</b>
        </RadioLabel>

        <RadioLabel checked={gender === "F"}>
          <input
            type="radio"
            name="gender"
            value="F"
            onChange={this.handleChange}
            checked={gender === "F"}
            required={required}
          />
          <b>MULHER</b>
        </RadioLabel>

        <RadioLabel checked={gender === "O"}>
          <input
            type="radio"
            name="gender"
            value="O"
            onChange={this.handleChange}
            checked={gender === "O"}
            required={required}
          />
          <b>OUTRO</b>
        </RadioLabel>
      </FormContainer>
    );
  }
}

export default GenderSelect;
