import React, { useContext } from "react";
import styled from "styled-components";
import Logo from "./Logo";
import MenuToggle from "./MenuToggle";
import LinkList from "./LinkList";
import { menusContext } from "contexts/menusContext";

const Div = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  z-index: 11;
  height: 176px;
  width: 105px;
  background: ${({ theme }) => theme.gradient(-90)};
  box-shadow: 2px 2px 10px 0 ${({ theme }) => theme.shadow};
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  &.open {
    height: 470px;
    width: 230px;
    padding-bottom: 30px;
  }
  @media (max-width: 768px) {
    &.closed {
      height: 70px;
      width: 70px;
      flex-direction: row-reverse;
      justify-content: space-between;
      padding: 0 15px;
      background: transparent;
      box-shadow: none;
    }
  }
`;
const Menu = () => {
  const { showNavMenu } = useContext(menusContext);
  let className = showNavMenu ? "open" : "closed";

  return (
    <Div className={className}>
      <Logo className={className} />
      <LinkList style={{ display: showNavMenu ? "block" : "none" }} />
      <MenuToggle className={className} />
    </Div>
  );
};

export default Menu;
