import styled from "styled-components";
import React from "react";

const Div = styled.div`
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  display: flex;
  justify-content: flex-start;
  align-content: center;
  margin-left: 13px;
  font-size: 24px;
  * {
    font: inherit;
  }
  i {
    font-weight: 400;
  }
  line-height: 26px;
  font-weight: 300;
  u {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }
  @media (max-width: 860px) {
    font-size: 22px;
  }
  @media (max-width: 768px) {
    margin-left: 10px;
    font-size: 16px;
  }
  @media (max-width: 640px) {
    font-size: 15px;
  }
`;

const VisitInfo = ({ date, time, address }) => (
  <Div>
    <i>
      {date} - {time}
    </i>
    <u>&nbsp;-&nbsp;{address}</u>
  </Div>
);

export default VisitInfo;
