import styled from "styled-components";

const Layout = styled.div`
  width: 1092px;
  margin: 135px auto;
  min-height: calc(100vh - 300px);
  @media (max-width: 1100px) {
    width: 100%;
    padding: 0 15px;
  }
  @media (max-width: 768px) {
    margin: 100px auto;
  }
  @media (max-width: 640px) {
    padding: 0;
  }
`;

export default Layout;
