import React from "react";
import styled from "styled-components";

const Li = styled.li`
  border-radius: 5px;
  background-color: ${({ theme }) => theme.bg};
  box-shadow: 0 18px 40px 0 ${({ theme }) => theme.shadowLight};
  margin-top: 30px;
  position: relative;
  h2 {
    padding: 30px;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    font-size: 20px;
    font-weight: 300;
    line-height: 21px;
    b {
      font: inherit;
    }
    span {
      transform: rotate(${({ open }) => (open ? 180 : 0)}deg);
      position: ${({ open }) => (open ? "absolute" : "static")};
      bottom: 30px;
      right: 30px;
      font-size: 20px;
      font-weight: 600;
    }
  }
  p {
    padding: 0 70px 50px 30px;
    font-size: 15px;
    font-weight: 300;
    line-height: 16px;
    text-align: justify;
  }
  @media (max-width: 768px) {
    margin-top: 25px;
    box-shadow: 0 4px 10px 0 ${({ theme }) => theme.shadowLight};
    h2 {
      padding: 15px;
      font-size: 12px;
      line-height: 13px;
      font-weight: 400;
      span {
        bottom: 15px;
        right: 15px;
      }
    }
    p {
      padding: 0 30px 40px 15px;
      font-size: 12px;
      line-height: 13px;
    }
  }
`;

const Info = ({ title, text }) => {
  let [open, setOpen] = React.useState(false);
  const toggle = () => setOpen(!open);

  return (
    <Li open={open}>
      <h2 onClick={toggle}>
        <b>{title}</b>
        <span className="icon-seta" />
      </h2>
      {open && <p>{text}</p>}
    </Li>
  );
};

export default Info;
