import React from "react";
import FormContainer from "components/common/Forms/FormContainer";
import FormInput from "components/common/Forms/FormInput";
import { credit_card } from "components/common/Forms/config";

const CCForm = ({ info, update }) => {
  const handleChange = ({ target }) => update({ [target.name]: target.value });

  return (
    <FormContainer>
      <FormInput
        value={info[credit_card[0]]}
        type="tel"
        mask="9999 9999 9999 9999"
        name={credit_card[0]}
        label="NÚMERO DO CARTÃO"
        autoComplete="cc-number"
        onChange={handleChange}
        pattern="\d{4} \d{4} \d{4} \d{4}"
        w="4"
        required
      />
      <FormInput
        value={info[credit_card[1]]}
        type="tel"
        mask="99/99"
        name={credit_card[1]}
        label="VENCIMENTO"
        autoComplete="cc-exp"
        onChange={handleChange}
        pattern="(0[1-9]|1[0-2])\/(19|2\d)"
        w="2"
        required
      />
      <FormInput
        value={info[credit_card[2]]}
        type="tel"
        mask="999"
        name={credit_card[2]}
        label="CÓDIGO"
        autoComplete="cc-exp"
        onChange={handleChange}
        pattern="\d{3}"
        w="2"
        required
      />
      <FormInput
        value={info[credit_card[3]]}
        type="text"
        name={credit_card[3]}
        label="NOME NO CARTÃO"
        autoComplete="cc-name"
        onChange={handleChange}
        w="4"
        required
      />
      <FormInput
        value={info[credit_card[4]]}
        type="text"
        name={credit_card[4]}
        label="CPF/CNPJ"
        mask="999.999.999-99"
        pattern="\d{3}\.\d{3}\.\d{3}-\d\d"
        onChange={handleChange}
        w="2"
        required
      />
      <FormInput
        value={info[credit_card[5]]}
        type="tel"
        name={credit_card[5]}
        label="TELEFONE"
        onChange={handleChange}
        w="2"
        mask="+(21) 99999-9999"
        pattern="\+\(21\) \d{5}-\d{3,4}"
        maskChar={null}
      />
    </FormContainer>
  );
};

export default CCForm;
