import React from "react";
import styled from "styled-components";
import formatMoney from "Utils/formatMoney";
import checkImg from "assets/icons/checkmark-circle.png";
import checkFillImg from "assets/icons/checkmark-circle-fill.png";

const Div = styled.div`
  border-radius: 5px;
  background-color: ${({ theme }) => theme.bgAlt};
  width: 100%;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  cursor: pointer;
  h5,
  span {
    font-size: 15px;
    font-weight: 300;
    line-height: 16px;
  }
  b {
    color: ${({ theme, cashed }) => (cashed ? theme.text : theme.primary)};
    text-decoration: ${({ cashed }) => cashed && "line-through"};
    font-size: 25px;
    font-weight: 700;
    line-height: 28px;
  }
  img {
    margin-left: 8px;
  }
  @media (max-width: 768px) {
    padding: 10px;
    h5,
    span {
      font-size: 10px;
      line-height: 11px;
    }
    b {
      font-size: 15px;
      line-height: 17px;
    }
  }
`;

const Cashback = ({ cashback, cashed, update }) => {
  const toggle = () => update({ cashed: !cashed });

  return (
    <Div cashed={cashed} onClick={toggle}>
      <div>
        <h5>CASHBACK</h5>
        <b>{formatMoney(cashback)}</b>
      </div>
      <div>
        <span>UTILIZAR COMO DESCONTO</span>
        <img src={cashed ? checkFillImg : checkImg} alt="check" />
      </div>
    </Div>
  );
};

export default Cashback;
