import React from "react";
import { Redirect } from "react-router-dom";
import Container from "components/common/Container";
import pieIcon from "assets/icons/pie-chart.png";
import Choices from "components/common/Choices";
import ButtonsContainer from "components/common/ButtonsContainer";
import SendProject from "./SendProject";
import TextArea from "components/common/TextArea";
import BudgetTiming from "./BudgetTiming";
import ErrorMsg from "components/common/Auth/ErrorMsg";
import format from "date-fns/format";

class NewBudgetType extends React.Component {
  state = {
    mode: 0,
    observation: "",
    date: format(new Date(), "yyyy-MM-dd", new Date()),
    time: "10:00"
  };

  choices = ["FAÇA VOCÊ MESMO", "ENVIAR PROJETO", "AGENDAR MEDIÇÃO"];
  update = obj =>
    this.setState(
      { ...obj, error: null },
      () => obj.mode === 1 && this.submit()
    );
  submit = ev => {
    if (ev) ev.preventDefault();
    let typeInfo = {};
    const { mode, files, date, time } = this.state;
    if (mode === 1) typeInfo = { diy: true };
    else {
      if (mode === 2) typeInfo = { files };
      else typeInfo = { date, time };
      typeInfo.observation = this.state.observation;
    }
    this.props.history.push(`/new_budget/name`, {
      ...this.props.location.state,
      typeInfo
    });
  };

  handleChange = ev => this.setState({ [ev.target.name]: ev.target.value });

  render() {
    const { state, update, submit } = this;
    if (!this.props.location.state) return <Redirect to="/new_budget" />;
    return (
      <Container icon={pieIcon} title="ENDEREÇO DE ENTREGA">
        <Choices choices={this.choices} update={update} mode={state.mode} />
        <ErrorMsg msg={state.error} />
        {state.mode > 1 && (
          <form onSubmit={submit} id="budgetType" style={{ width: "100%" }}>
            {state.mode === 2 && <SendProject update={update} />}
            {state.mode === 3 && (
              <BudgetTiming
                update={update}
                date={state.date}
                time={state.time}
              />
            )}
            <TextArea
              rows="3"
              placeholder="OBSERVAÇÃO DO PROJETO"
              required
              name="observation"
              value={state.observation}
              onChange={this.handleChange}
            />
          </form>
        )}
        <ButtonsContainer
          mode={state.mode}
          actionName="ENVIAR"
          formId="budgetType"
        />
      </Container>
    );
  }
}

export default NewBudgetType;
