import React, { useState } from "react";
import { withRouter } from "react-router-dom";
import styled from "styled-components";
import deleteVisitIcon from "assets/icons/agenda-delete.png";

const Div = styled.div`
  border: 1px solid ${({ theme }) => theme.error};
  cursor: pointer;
  padding: 10px;
  border-radius: 5px;
  img {
    margin-right: 10px;
  }
  b {
    font-size: 18px;
    font-weight: 300;
    line-height: 19px;
    color: ${({ theme }) => theme.error};
  }
`;

const DivConfirm = styled.div`
  text-align: center;
  h4 {
    font-size: 12px;
    font-weight: 300;
    line-height: 13px;
    margin-bottom: 5px;
  }
  span {
    padding: 10px 20px;
    border-radius: 5px;
    background-color: ${({ theme }) => theme.grey};
    color: ${({ theme }) => theme.white};
    display: inline-block;
    margin: 0 12px;
    cursor: pointer;
  }
  span.icon-fechar {
    background: ${({ theme }) => theme.primary};
  }
`;

const DeleteVisit = ({ id, history }) => {
  const [confirm, setConfirm] = useState(false);
  const remove = () => {
    const data = JSON.parse(sessionStorage.getItem("deletedVisits")) || [];
    data.push(id);
    sessionStorage.setItem("deletedVisits", JSON.stringify(data));
    history.push("/visits");
  };

  return confirm ? (
    <DivConfirm>
      <h4>CONFIRMAR CANCELAMENTO</h4>
      <span className="icon-check" onClick={remove} />
      <span className="icon-fechar" onClick={() => setConfirm(false)} />
    </DivConfirm>
  ) : (
    <Div onClick={() => setConfirm(true)}>
      <img src={deleteVisitIcon} alt="delete" />
      <b>CANCELAR</b>
    </Div>
  );
};

export default withRouter(DeleteVisit);
