import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import { login } from "Utils/Connect";
import TextInput from "components/common/Auth/TextInput";
import Button from "components/common/Auth/Button";

export default withRouter(
  class LoginForm extends Component {
    state = {
      wrongAuth: false,
      email: "",
      password: ""
    };

    handleChange = ev => this.setState({ [ev.target.name]: ev.target.value });
    
    handleSubmit = async ev => {
      ev.preventDefault();
      if (await login(this.state.email, this.state.password))
        this.props.history.push("/");
      else this.props.setWrongAuth(true);
    };

    render() {
      return (
        <form onSubmit={this.handleSubmit}>
          <TextInput
            type="email"
            placeholder="Email"
            autoComplete="email"
            required
            value={this.state.email}
            name="email"
            onChange={this.handleChange}
          />
          <TextInput
            type="password"
            placeholder="Senha"
            autoComplete="current-password"
            required
            value={this.state.password}
            name="password"
            onChange={this.handleChange}
          />
          <Button type="submit">Entrar</Button>
        </form>
      );
    }
  }
);
