import styled from "styled-components";

const RadioLabel = styled.label`
  text-align: center;
  width: calc((100% - 30px) / 3);
  b {
    color: ${({ theme, checked }) => (checked ? theme.primary : theme.text)};
    background-color: ${({ theme }) => theme.bgAlt};
    font-size: 16px;
    font-weight: ${({ checked }) => (checked ? "700" : "300")};
    line-height: 17px;
    line-height: 50px;
    display: block;
    width: 100%;
    border-radius: 8px;
  }

  input {
    display: none;
  }
`;

export default RadioLabel;
