import React from "react";
import styled from "styled-components";
import OptionsMenu from "components/common/OptionsMenu";
import moreIcon from "assets/icons/icone_more.png";
import deleteIcon from "assets/icons/icone_excluir.png";

const Div = styled.div`
  background-color: ${({ theme, remove }) =>
    remove ? theme.error : theme.bgAlt};
  color: ${({ theme, remove }) => (remove ? theme.white : theme.text)};
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  font-size: 20px;
  line-height: 21px;
  font-weight: 300;
  > u,
  .name,
  > img,
  > span {
    font: inherit;
    color: inherit;
    margin: 0 15px;
  }
  u img {
    margin-right: 10px;
    filter: brightness(10);
    cursor: pointer;
  }
  .name {
    margin: 0;
  }
  @media (max-width: 768px) {
    font-size: 15px;
    line-height: 16px;
    height: 40px;
  }
`;

const ListTitle = ({ name, mode, actions, setMode }) => {
  const openMenu = () => setMode(1);

  const options = {
    close: () => setMode(0),
    edit: () => setMode(2),
    duplicate: () => {
      setMode(0);
      actions.duplicate(name);
    },
    remove: () => setMode(3)
  };

  const remove = () => actions.removeList(name);

  return (
    <Div remove={mode === 3}>
      <u>
        {mode === 3 && <img src={deleteIcon} alt="delete" onClick={remove} />}
        <b className="name">{name}</b>
      </u>
      {mode === 0 && <img src={moreIcon} alt="..." onClick={openMenu} />}
      {mode === 1 && <OptionsMenu {...options} />}
      {mode > 1 && <span className="icon-fechar" onClick={options.close} />}
    </Div>
  );
};

export default ListTitle;
