import React from "react";
import styled from "styled-components";
import NotificationsHeader from "./NotificationsHeader";
import NotificationsBody from "./NotificationsBody";

const Div = styled.div`
  position: absolute;
  top: 40px;
  left: -150px;
  height: 400px;
  width: 324px;
  max-width: 324px;
  background: ${({ theme }) => theme.bg};
  filter: drop-shadow(${({ theme }) => theme.shadowLight} 0 2px 5px);
  border-radius: 5px;
  &:before {
    border: 10px solid;
    border-color: transparent transparent ${({ theme }) => theme.bg};
    content: "";
    left: 50%;
    margin-left: -10px;
    position: absolute;
    bottom: 100%;
  }
  @media (max-width: 768px) {
    left: initial;
    right: -50px;
    width: calc(100vw - 40px);
    &:before {
      left: initial;
      right: 52px;
    }
  }
`;

const NotificationsList = ({ notifications }) => (
  <Div>
    <NotificationsHeader notificationsCount={notifications.length} />
    <NotificationsBody notifications={notifications} />
  </Div>
);

export default NotificationsList;
