import React from "react";
import CardContainer from "./card2.styles";
import arrowIcon from "assets/icons/arrow.png";
import getStatusFromColor from "Utils/getStatusFromColor";

const Card2 = ({ statusColor, status, id, client }) => (
  <CardContainer bg={statusColor} to={`/orders/${id}`}>
    <div className="topBar" />

    <dl>
      <dt>PEDIDO</dt>
      <dd className="alt">{id}</dd>

      <dt>OBRA</dt>
      <dd>{client}</dd>

      <dt>STATUS</dt>
      <dd>{getStatusFromColor(statusColor)}</dd>
      <span title={status}>
        <b>{status}</b>
        <img src={arrowIcon} alt="->" />
      </span>
    </dl>
  </CardContainer>
);

export default Card2;
