import React from "react";
import styled from "styled-components";
import ActionButton from "components/common/ActionButton";
import OwnerInfo from "components/common/OwnerInfo";

const Div = styled.div`
  display: flex;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  text-transform: uppercase;
  h3 {
    font-size: 25px;
    font-weight: 300;
    line-height: 27px;
    margin: 5px 0 10px 0;
  }
  > div {
    flex-grow: 1;
    width: 100%;
  }
  a {
    width: 100%;
    margin: 0;
  }
  @media (max-width: 768px) {
    flex-direction: column;
    text-align: center;
    a {
      margin-top: 10px;
    }
  }
`;

const TopInfo = ({ id, client, address, isOrder }) => (
  <Div>
    <div>
      <h3>{id}</h3>
      <OwnerInfo client={client} address={address} />
    </div>
    <div>
      {!isOrder && (
        <ActionButton text="ADICIONAR PRODUTO" to={`./${id}/products`} drown />
      )}
    </div>
  </Div>
);

export default TopInfo;
