import React from "react";
import FormInput from "./FormInput";
import FormContainer from "./FormContainer";
import { security, toObj } from "./config";

class SecurityForm extends React.Component {
  state = {
    ...toObj(security),
    ...this.props.info
  };
  handleChange = ev => {
    this.setState({ [ev.target.name]: ev.target.value });
    this.props.update({ [ev.target.name]: ev.target.value });
  };
  render() {
    const { required, emailOnly } = this.props;
    return (
      <FormContainer>
        <FormInput
          value={this.state[security[0]]}
          type="email"
          name={security[0]}
          label="EMAIL"
          onChange={this.handleChange}
          w="4"
          autoComplete="email"
          required={required}
        />
        {!emailOnly && (
          <>
            <FormInput
              value={this.state[security[1]]}
              type="password"
              name={security[1]}
              label="SENHA"
              onChange={this.handleChange}
              w="4"
              autoComplete="new-password"
              required={required}
            />
            <FormInput
              value={this.state[security[2]]}
              type="password"
              name={security[2]}
              label="CONFIRMAR SENHA"
              onChange={this.handleChange}
              w="4"
              autoComplete="new-password"
              required={required}
            />
          </>
        )}
      </FormContainer>
    );
  }
}

export default SecurityForm;
