import React from "react";
import { Redirect } from "react-router-dom";
import Container from "components/common/Container";
import TopInfo from "../TopInfo";
import { getOrderInfo } from "Utils/Connect";
import Hider from "components/Hider";
import { userContext } from "contexts/userContext";
import ButtonsContainer from "components/common/ButtonsContainer";
import CCForm from "./CCForm";
import { credit_card, toObj } from "components/common/Forms/config";
import Done from "components/common/Done";

class PayCreditCard extends React.Component {
  state = { loading: true, ...toObj(credit_card) };
  id = this.props.match.params.id;

  static contextType = userContext;

  update = obj => this.setState(obj);

  submit = ev => {
    ev.preventDefault();
    this.setState({ done: true });
  };

  async componentDidMount() {
    const response = await getOrderInfo(this.id, this.context.access_token);
    if (!response) return this.setState({ notFound: true });
    this.setState({ loading: false, ...response });
  }

  render() {
    if (this.state.notFound)
      return <Redirect to="/notFound?title=Não encontramos o identificador de order" />;
    if (this.state.loading) return <Hider />;
    if (this.state.done)
      return (
        <Done
          title="PAGAMENTO EFETUADO"
          text="VOCÊ RECEBERÁ A CONFIRMAÇÃO NO EMAIL CADASTRADO."
          subText={`COMPROVANTE: ${this.id}`}
          to="./"
        />
      );
    const { order } = this.state;
    return (
      <Container>
        <TopInfo {...order} label="CARTÃO DE CRÉDITO" />
        <form id="cc_form" style={{ width: "100%" }} onSubmit={this.submit}>
          <CCForm update={this.update} info={this.state} />
        </form>
        <ButtonsContainer mode="2" actionName="PAGAR" formId="cc_form" />
      </Container>
    );
  }
}

export default PayCreditCard;
